<template>
  <div class="detaicont">
    <div class="progress">
      <img :src="tMusic.pictureUrl" alt="" :onerror="defaultImg">
      <div class="gressbar">
        <span class="curr">{{time}}</span>
        <span class="total">/{{totle}}</span>
      </div>
    </div>
    <p class="name">{{tMusic.collectionName}}</p>
    <p class="mumu">{{tMusic.museumName}}</p>
    <p class="introduce">
     {{tMusic.description}}
    </p>
    <div class="player">
      <div class="playcont">
        <img class="ico" src="../../assets/images/guide/playpre.png" alt="" @click.stop="isPre">
        <img class="icos" src="../../assets/images/guide/playstop.png" alt="" v-show="!playorpause" @click="isPlay">
        <img class="icos" src="../../assets/images/guide/playplay.png" alt="" v-show="playorpause" @click="isStops">
        <img class="ico" src="../../assets/images/guide/playnext.png" alt="" @click.stop="isNext">
      </div>
      <!--<audio ref="audio" style="display: none" id="player"    preload autoplay @ended="isNext"></audio>-->
    </div>
  </div>
</template>

<script>
  import { mapState } from "vuex";
    export default {
        name: "themeDetail",
      data(){
          return{
            time:0,
            totle:0,
            index:0,
            tList:[],
            tMusic:[],
            tPlaying:"",
            tIndex:0,
            playorpause:false,
            defaultImg: this.$store.getters.defaultImg,
            bar:0,
            timer:null,
            currentId:"",
            collectionId:"",
            tourId:"",
            currentData:{},
          }
      },
      computed: mapState({
        count:state=>state.audio.audioIndex
      }),
      watch:{
        'index':'getMusic',
        'count':'getMusic',
      },
      mounted(){
        let _this = this;
        _this.collectionId=_this.$route.query.collectionId;
        _this.tourId=_this.$route.query.tourId;
        var audio = document.getElementById("audio");
        var gressbar = document.getElementsByClassName("gressbar");
        var w = gressbar[0].style.width;
        var W=document.body.clientWidth-w;
        this.timer=setInterval(function (){
          _this.time = audio && audio.currentTime && _this.formatSeconds(audio.currentTime);
          _this.totle = audio && audio.duration && _this.formatSeconds(audio.duration)||0;

          if(audio && audio.currentTime && audio.duration&&!audio.paused){
            gressbar[0].style.left=audio.currentTime/audio.duration*W+"px"
          }

        },1000);
       /* this.$once('hook:beforeDestroy', () => {
          clearInterval(timer);
          timer.destroy()
        })*/
        this.init();
      },
      beforeRouteLeave(to,from,next){
      //  document.getElementById("player").pause();
       // next()
      },
      beforeDestroy(){

          if(this.timer){
            clearInterval(this.timer);
            this.timer=null;
          }
      },
      methods:{
        init(){
          var _this=this;
         // document.getElementById("player").pause();
          document.getElementById("audio").currentTime="0";
          this.tMusic="";
          this.$http.get(`/m/Guide/getCollectInfoList.do`,{"collectionId":_this.collectionId,"tourId":this.tourId}).then(response => {
            _this.tList=response.data.data.collectionList;
            _this.index=response.data.data.showNum-1;
            _this.$nextTick(()=> {
              _this.$store.state.audio.MList=response.data.data.collectionList;
              // _this.$store.state.audio.music=this.videoList[0];
              _this.$store.state.audio.music = response.data.data.collectionList[response.data.data.showNum - 1];
              _this.tMusic = response.data.data.collectionList[response.data.data.showNum - 1];
              //  _this.count.audioshow = false;
               // _this.$store.dispatch('isPause');
             // _this.$store.dispatch('isPlay');
              //  document.getElementById("player").play();
              setTimeout(()=>{
                _this.$store.dispatch('isPlay');
              }, 0)
                _this.playorpause = true;
              })
          })
        },
        getMusic(p){
          this.tMusic="";
          document.getElementById("audio").currentTime="0";
          if(p < this.$store.state.audio.MList.length) {
            // this.music = response.music[p]
            this.tMusic = this.$store.state.audio.MList[p]
            this.$store.state.audio.music = this.$store.state.audio.MList[p]
          }
          if(p >= this.$store.state.audio.MList.length){
            this.index = 0;
           p = 0;
            this.tMusic = this.$store.state.audio.MList[p]
            this.$store.state.audio.music = this.$store.state.audio.MList[p]
          }
          if(p < 0){
            this.index = this.$store.state.audio.MList.length;
            p = this.$store.state.audio.MList.length;
            this.tMusic = this.$store.state.audio.MList[p]
            this.$store.state.audio.music = this.$store.state.audio.MList[p]
          }
          setTimeout(()=>{
            this.$store.dispatch('isPlay');
          }, 0)
          //document.getElementById("audio").play();
          this.playorpause=true;
        },
        isPlay(index){
            /* this.count.audioshow=false;
             this.count.playing=false;*/
          setTimeout(()=>{
            this.$store.dispatch('isPlay');
          }, 0)

            // document.getElementById("player").play();
            this.playorpause=true;
          return false
        },
        isStops(){
        /*  this.count.audioshow=true;
          this.count.playing=true;*/
          this.$store.dispatch('isPause');
            // document.getElementById("player").pause();
            this.playorpause=false;

          return false
        },
        isNext(){
          this.index++;
          this.playorpause=true;
        },
        isPre(){
          this.index--;
          this.playorpause=true;
        },
        formatSeconds(value) {
          var hh = parseInt(value/3600);
          if(hh<10) hh = "0" + hh;
          var mm = parseInt((value-hh*3600)/60);
          if(mm<10) mm = "0" + mm;
          var ss = parseInt((value-hh*3600)%60);
          if(ss<10) ss = "0" + ss;
          // var length = hh + ":" + mm + ":" + ss;
          var length =  mm + ":" + ss;
          if(value>0){
            return length;
          }else{
            return "NaN";
          }
        }
      }
    }
</script>

<style scoped>
  .player{
    position: fixed;
    bottom:0rem;
    width: 100%;
    height: 3.6rem;
    background-color: white;
  }
  .playcont{
    display: flex;
    height: 2.1rem;
    justify-content: space-around;
    width: 9.5rem;
    margin: 0 auto;
  }
  .player .ico{
    width: .9rem;
    height: .8rem;
    margin-top: .65rem;
  }
  .player .icos{
    width: 2.1rem;
    height: 2.1rem;
  }
.detaicont{
padding-bottom: 5rem;
  position: relative;
}
.mumu{
  width: 16rem;
  height:0.9rem;
  font-size:0.65rem;
  font-family:PingFang-SC-Regular;
  font-weight:400;
  color:rgba(185,185,185,1);
  margin: 0 auto 1.5rem;
  line-height:0.9rem;
  text-align: center;
}
.introduce{
  width:16.25rem;
  margin: 0 auto;
  font-size:0.7rem;
  font-family:PingFangSC-Regular;
  font-weight:400;
  color:rgba(102,102,102,1);
  line-height:1.5rem;
  text-indent: 1.4rem;
}
.name{
  width: 16rem;
 margin: 1.25rem auto .25rem;
  height:1.05rem;
  font-size:0.75rem;
  text-align: center;
  font-family:PingFang-SC-Bold;
  font-weight:bold;
  color:rgba(102,102,102,1);
  line-height:1.05rem;
}
  .progress{
    position: relative;
  }
  .progress img{
    width: 100%;
    height: 14.05rem;
  }
  .gressbar{
    position: absolute;
    bottom: 0;
    left: 0;
    height:0.9rem;
    background:rgba(0,0,0,1);
    box-shadow:0.1rem 0.1rem 0.3rem 0rem rgba(0,0,0,0.5);
    border-radius:0.45rem;
    opacity:0.7;
    padding: .1rem .4rem;
  }
  .curr{
    height:0.7rem;
    font-size:0.5rem;
    font-family:PingFang-SC-Regular;
    font-weight:400;
    color:rgba(153,153,153,1);
    line-height:0.8rem;
    vertical-align: top;
  }
  .total{
    height:0.7rem;
    font-size:0.5rem;
    font-family:PingFang-SC-Regular;
    font-weight:400;
    color:rgba(153,153,153,1);
    line-height:0.8rem;
    vertical-align: top;
  }
</style>



