<template>
  <div id="audiolist">
    <ul class="ulaudio">
      <li v-for="(item, index) in $store.state.audio.MList" :key="index" :class="{'itemactive':index == $store.state.audio.audioIndex }" >
          <div class="num">
              <span class="numindex"> {{index+1}}</span>
          </div>
          <div class="listcon">
              <div class="leftbox">
                <p class="title ellipse">{{item.collectionName}}</p>
                <p class="time">
                     <span></span>
                     <img src="../../assets/images/guide/timer.png" alt="">
                     <span v-show="index == $store.state.audio.audioIndex && $store.state.audio.audioshow">{{time}}</span>
                </p>
              </div>
              <div class="rightbox">
                <img class="playicon" v-if="state||(showPlayStop||index != $store.state.audio.audioIndex)" src="../../assets/images/guide/zhan.png" @click="isPlay(index)">
                <img class="stopicon" v-if="!state&&(!showPlayStop&&index == $store.state.audio.audioIndex)" src="../../../static/images/audio/detailstop.svg" @click="isStop">
              </div>
          </div>
      </li>
    </ul>
    <div v-if="$store.state.audio.MList.length === 0">
      <div class="g-no-data" style="margin-top:0;">
        <div class="no-data-bg"></div>
        <p class="no-data-txt">暂无数据</p>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState, mapMutations, mapGetters } from "vuex";
export default {
    name: 'app',
    data(){
        return{
         apiUrl: 'http://www.egtch.com/t_works/Vuedata/data.php',
          id: 0,
          time:0,
          showPlayStop:false,
          state:true
        }
    },
    props:{
      videoList:Array
    },
    mounted(){
        let _this = this;
      if( document.getElementById("audio").play){
        document.getElementById("audio").pause();
        document.getElementById("audio") && (document.getElementById("audio").currentTime=0)
      }
      setInterval(function (){
        var audio = document.getElementById("audio");
        _this.time = audio && audio.currentTime && _this.formatSeconds(audio.currentTime);
      },1000);
      //this.getMusic(this.$store.state.audio.mutations.audioIndex);
       // this.getMusic(this.id);
      this.init(this.id);
    },
    computed: mapState({
      count:state=>state.audio.playing
    }),
  /*computed: {
    ...mapState('audio',[
      'audioIndex',
      'music',
      'MList',
      'audioshow'
    ])
  },*/
    watch:{
      count:function (newval,oldval) {
     if(this.$store.state.audio.audioshow){
       if(newval){
         this.showPlayStop=false;
       }else{
         this.showPlayStop=true;
       }
     }else{
       this.showPlayStop=true;
     }

      },
        'id':'getMusic',
    },
  activated() {
    this.init(this.id);
  },
    methods:{
        init(p){
          var _this=this;
          this.state=true;
            _this.data=this.videoList;
            _this.$store.state.audio.MList=this.videoList;
            _this.$store.state.audio.music=this.videoList[0];
            //this.$store.dispatch('showAudio');
          if( document.getElementById("audio").play){
            _this.$store.dispatch('isPause');
          }
           this.id = this.$store.state.audio.audioIndex
        },
      getMusic(p){
        
        this.$store.state.music="";
        document.getElementById("audio").currentTime="0";
        if(p < this.$store.state.audio.MList.length) {
          // this.music = response.music[p]
          this.$store.state.audio.music = this.$store.state.audio.MList[this.$store.state.audio.audioIndex]
        }
        if(p > this.$store.state.audio.MList.length){
          this.id = 0
          this.$store.state.audio.audioIndex = 0
          this.$store.state.audio.music = this.$store.state.audio.MList[this.$store.state.audio.audioIndex]
        }
        if(p < 0){
          this.id = this.$store.state.audio.MList.length
          this.$store.state.audio.audioIndex = this.$store.state.audio.MList.length
          this.$store.state.audio.music = this.$store.state.audio.MList[this.$store.state.audio.audioIndex]
        }
        //})

      },
        /*查看当前audio是否正在播放，如果停止，那么播放下一曲*/
        isPlay(index){
            this.id = index;
           this.showPlayStop=false;
           this.state=false;
            this.$store.state.audio.playing=true;
            this.$store.state.audio.audioIndex = index;
            this.$store.dispatch('showAudio');
            setTimeout(function () {
              if(document.getElementById("audio").paused){
                // document.getElementById("audio").src.autoplay=true
                //   document.getElementById("audio").load()
                document.getElementById("audio").play();
              }
            },500)
            return false
        },
        isStop(){
            document.getElementById("audio").pause();
            this.showPlayStop=true;
            this.state=true;
          this.$store.state.audio.playing=false;
            return false
        },
      formatSeconds(value) {
        var hh = parseInt(value/3600);
        if(hh<10) hh = "0" + hh;
        var mm = parseInt((value-hh*3600)/60);
        if(mm<10) mm = "0" + mm;
        var ss = parseInt((value-hh*3600)%60);
        if(ss<10) ss = "0" + ss;
        // var length = hh + ":" + mm + ":" + ss;
        var length =  mm + ":" + ss;
        if(value>0){
          return length;
        }else{
          return "NaN";
        }
      }
    }
}
</script>

<style scoped>
  #audiolist{
    clear: both;
  }
.ulaudio{
  margin-top: 1rem;

}
.ulaudio li{
  margin: 0 0.2rem;
  display: flex;
  flex-direction: row;
  height: 3rem;
  border-bottom:1px solid rgba(236,240,239,1);
  padding: .45rem 1.25rem .4rem;
}
.num{
  width: 0.49rem;
  height: 1rem;
  line-height: 1rem;
  font-size:0.7rem;
  font-family:PingFang-SC-Regular;
  font-weight:400;
  color:rgba(102,102,102,1);
  margin-right: 1rem;
  margin-top: .5rem;
}
.num img{
  width: 0.24rem;
  height: 0.32rem;
  vertical-align: middle;
}
.listcon{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

}
.listcon .leftbox{
  width: 9rem;
}
.listcon .leftbox .title{
  height:1rem;
  font-size:0.7rem;
  font-family:PingFangSC-Regular;
  font-weight:400;
  color:rgba(102,102,102,1);
  line-height:1rem;
}
.listcon .leftbox .time{
    margin-top: 0.2rem;
    height: 1rem;
    line-height: 1rem;
}
.listcon .leftbox .time img{
    height: 0.5rem;
    width: 0.5rem;
    margin-right: 0.12rem;
}
  .listcon .leftbox .time span{
    font-size:0.6rem;
    font-family:PingFang-SC-Regular;
    font-weight:400;
    color:rgba(185,185,185,1);
    line-height:0.85rem;
  }
  .listcon .leftbox .time span:nth-child(3).active{

  }
  .listcon .rightbox{
  margin-top: 0.1rem;
}
.listcon .rightbox img{
  width:1rem;
  height:1rem;
  margin-top: .5rem;
}
.speakericon{display: none;}
.stopicon{display: none;}

.itemactive .speakericon{display: inline;}

.itemactive .stopicon{display: inline;}

</style>



