<template>
   <div class="mycontent">
     <div class="title">
       <span></span>
       <span>展览信息</span>
     </div>
     <div class="cont">
       <img src="../../assets/images/guide/mum.png" alt="">
       <span>{{Mydata.tourDetailInfo&&Mydata.tourDetailInfo.museumName}}</span><br>
       <img src="../../assets/images/guide/zhanl.png" alt="">
       <span>{{Mydata.tourDetailInfo&&Mydata.tourDetailInfo.name}}</span><br>
       <img src="../../assets/images/guide/key.png" alt="">
       <span class="maBtn" v-if="Mydata.tourDetailInfo&&Mydata.tourDetailInfo.label&&Mydata.tourDetailInfo.label.split(',').length>0">{{Mydata.tourDetailInfo&&Mydata.tourDetailInfo.label&&Mydata.tourDetailInfo.label.split(",")[0]}}</span>
       <span class="maBtn" v-if="Mydata.tourDetailInfo&&Mydata.tourDetailInfo.label&&Mydata.tourDetailInfo.label.split(',').length>1">{{Mydata.tourDetailInfo&&Mydata.tourDetailInfo.label&&Mydata.tourDetailInfo.label.split(",")[1]}}</span>
     </div>
     <div class="title">
       <span></span>
       <span>展期</span>
     </div>
     <div class="cont">
       <p>{{Mydata.tourDetailInfo&&Mydata.tourDetailInfo.tourTime}}</p>
     </div>
     <div class="title">
       <span></span>
       <span>展厅</span>
     </div>
     <div class="cont">
       <p>{{Mydata.tourDetailInfo&&Mydata.tourDetailInfo.address}}</p>
     </div>
     <div class="title">
       <span></span>
       <span>简介</span>
     </div>
     <div class="cont">
       <p>{{Mydata.tourDetailInfo&&Mydata.tourDetailInfo.introduction}}</p>
     </div>
   </div>
</template>

<script>
    export default {
        name: "tabdetail",
      data(){
          return{

          }
      },
      props:{
        Mydata:Object
      }
    }
</script>

<style scoped lang="less">
.mycontent{
  clear: both;
  .title{
    height:2rem;
    padding: .5rem 0;
    margin-bottom: .5rem;
    span:nth-child(1){
      width:0.19rem;
      height:0.65rem;
      background:rgba(72,165,255,1);
      display:block;
      float: left;
      margin-top: .175rem;
    }
    span:nth-child(2){
      font-size:0.7rem;
      font-family:PingFangSC-Regular;
      font-weight:400;
      color:rgba(40,40,40,1);
      line-height:1rem;
      vertical-align: top;
      margin-left: 1rem;
    }
  }
  .cont{
    margin-left: 1.3rem;
    margin-bottom: 1rem;
    p{
      width:16.25rem;
      font-size:0.7rem;
      font-family:PingFangSC-Regular;
      font-weight:400;
      color:rgba(102,102,102,1);
      line-height:1rem;
    }
    span{
      font-size:0.7rem;
      font-family:PingFangSC-Regular;
      font-weight:400;
      color:rgba(102,102,102,1);
      line-height:1rem;
      vertical-align: top;
      margin-left: .65rem;
    }
    .maBtn{
      height:1.05rem;
      font-size:0.65rem;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      color:rgba(102,102,102,1);
      line-height:1.05rem;
      border-radius:0.6rem;
      padding: .05rem .5rem;
      border:0.05rem solid #979797;
    }
  }
}
</style>



