<template>
  <div class="main">
    <div class="title">
      <img class="list" src="../../assets/images/museum/list.png" alt="" @click="we()">
      <input ref="lk" type="text" placeholder="输入展览名称" maxlength="10">
      <div @click="ss()"></div>
    </div>
    <div class="cont" :class="{cur:$store.state.audio.audioshow}">
      <div class="chenlie">
        <div class="line"></div>
        <div class="cont_wrap">
          <div class="item" v-for="(di,index) in chenlie">
            <router-link :to="{path:'/wenchuangDetail',query:{id:di.id}}">
              <img class="dis" :src="di.pictureId" alt="">
              <p><span>{{di.productName}}</span>  <i></i></p>
              <div>{{di.productPrice}}</div>
            </router-link>
          </div>
        </div>
        <div class="more" @click="more()">查看更多</div>
      </div>
    </div>
    <pop :po="popp"></pop>
  </div>
</template>

<script>
  import { Toast } from 'mint-ui';
  import pop from '../../components/popup';
  export default {
    name: "index",
    data(){
      return{
        pageNum:1,
        totalNum:1,
        chenlie:[],
        size:8,
        popp:false,
        quyuId:"",
        museumId:"",
        key:""
      }
    },
    components:{
      pop
    },
    methods:{
      ss(){
        this.key=this.$refs.lk.value;
        this.$http.get("/m/wenChuang/getList.do",{currentPage:this.pageNum,size:this.size,area:this.quyuId,orgId:this.museumId,key:this.key}).then((res)=>{

          this.pageNum=1;
          this.chenlie=res.data.data;
          this.totalNum=res.data.page.totalPage;
        })
      },
      ch(){
        this.popp=false;
      },
      we(){
        if(this.popp){
          this.popp=false;
        }else{
          this.popp=true;
        }
      },
      getData(quyuId,museumId){
        this.popp=false;
        this.pageNum=1;

        this.quyuId=quyuId;
        this.museumId=museumId;
        this.$http.get("/m/wenChuang/getList.do",{currentPage:this.pageNum,size:this.size,area:this.quyuId,orgId:this.museumId,key:this.key}).then((res)=>{

          this.chenlie=res.data.data;
          this.totalNum=res.data.page.totalPage;
        })
      },
      more(){
        if(this.pageNum==this.totalNum){
          Toast('已全部加载');
          return false;
        }else{
          this.pageNum++;
          this.$http.get("/m/wenChuang/getList.do",{currentPage:this.pageNum,size:this.size,area:this.quyuId,orgId:this.museumId,key:this.key}).then((res)=>{
            ;
            this.chenlie=this.chenlie.concat(res.data.data);
          })
        }
      },

    },
    mounted(){
      this.$http.get("/m/wenChuang/getList.do",{currentPage:this.pageNum,size:this.size}).then((res)=>{
        ;
        this.chenlie=res.data.data;
        this.totalNum=res.data.page.totalPage;
      })


    }
  }
</script>

<style lang="less" scoped>
  .main {
    width: 100%;
    .title {
      margin-top: 1px;
      height: 3.5rem;
      padding-top: 1rem;
      padding-left: 1.25rem;
      background: #fff;
      position: relative;
      .list {
        height: 1.5rem;
      }
      input {
        width: 13.85rem;
        height: 1.5rem;
        border-radius: 0.75rem;
        vertical-align: top;
        margin-left: 0.8rem;
        font-size: 0.6rem;
        font-weight: 400;
        color: rgba(185, 185, 185, 1);
        line-height: 1.5rem;
        padding-left: 1.8rem;
        background: rgb(241, 241, 241) url("../../assets/images/museum/sousuo.png") no-repeat 0.45rem 0.3rem;
      }
      div{
        position: absolute;
        width: 1.3rem;
        height: 1.3rem;
        top: 1.25rem;
        left:4rem;
        z-index: 100;
      }
    }
    .line{
      height: 0.5rem;
      background: rgba(241, 241, 241, 1);
    }
    .cont{
      width: 100%;
      background-color: #fff;
      position: absolute;
      top: 4rem;
      bottom: 0;
      overflow-y: scroll;
      &.cur{
        top: 6rem;
      }
      .chenlie{
        .cont_wrap{
          width: 16.25rem;
          margin: 0 auto;
          padding-top: 1rem;
          &:after{
            content: '';
            display: block;
            clear: both;
          }
          .ti{
            font-size:0.85rem;
            font-weight:bold;
            color:rgba(40,40,40,1);
            margin-bottom: 1rem;
          }
          .item{
            margin-bottom: 2rem;
            width: 7.5rem;
            float: left;
            margin-right: 1.25rem;
            &:nth-child(2n){
              margin-right: 0;
            }
            .dis{
              width:7.5rem;
              height:7.5rem;
              border-radius:0.25rem;
            }
            p{
              height:1rem;
              margin: 0.5rem 0;
              i{
                display: inline-block;
                float: right;
                margin-right: 0.2rem;
                margin-top: 0.2rem;
                width: 0.7rem;
                height: 0.7rem;
                background: url("../../assets/images/wenchuang/pay.png");
                background-size: 100% 100%;
              }
              span{
                overflow: hidden;
                text-overflow:ellipsis;
                white-space: nowrap;
                width: 6rem;
                display: inline-block;
                font-size:0.75rem;
                font-family:PingFang-SC-Bold;
                font-weight:bold;
                color:rgba(102,102,102,1);
                line-height:1.05rem;
              }
            }
            div{
              font-size:0.6rem;
              font-weight:400;
              color:rgba(233,67,53,1);
              line-height:0.85rem;
            }
          }
        }
        .more{
          width:16.25rem;
          height:1.5rem;
          background:rgba(243,245,250,1);
          border-radius:0.25rem;
          font-size:0.65rem;
          text-align: center;
          font-weight:bold;
          color:rgba(91,102,160,1);
          line-height:1.5rem;
          margin:0 auto 1.3rem;
          touch-action: none;
        }
      }
    }

  }
</style>



