<template>
  <div id="app">
    <div class="header">
      <!-- <img src="./assets/images/logo.png"> -->
    </div>
    <div class="app-main">
      <!--<myaudio v-show="$store.state.audio.audioshow"></myaudio>-->
      <myaudio v-show="audio.audioshow"></myaudio>
      <keep-alive>
        <router-view v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
      <router-view v-if="!$route.meta.keepAlive"></router-view>
    </div>
  </div>
</template>

<script>
  import myaudio from '@/components/audio/audioTop'
  import { mapState } from 'vuex'
export default {
    data(){
      return{
        audioTopIndexClass:false
      }
    },
  computed: mapState({
        audio: state => state.audio
    }
    ),
  mounted(){

  },
  components: {
    myaudio
  },
  name: 'App'
}
</script>

<style lang="less">
  @import './assets/css/theme.less';
  @import 'normalize.css';
  @import 'mint-ui/lib/style.css';
  @import 'flex.css';
</style>



