<template>
  <div class="main">
    <img class="di" :src="list.pics&&list.pics[0]&&list.pics[0].url" alt="">
    <div class="shoucang" v-show="iscollected==0" @click="chou(1,list.id)">收藏</div>
    <div class="shoucang active" v-show="iscollected==1" @click="chou(2,list.id)">已收藏</div>
    <div class="cont">
      <div class="ti"><span v-show="type">{{list.collectionLevelDes}}</span><span>{{list.name}}</span></div>
      <div class="time"><img src="../../assets/images/activity/time.png" alt=""><span>{{list.yearName}}</span></div>
      <div class="zhonglei"><img src="../../assets/images/activity/zhonglei.png" alt=""><span>{{list.categoryName}}</span></div>
      <div class="adress"><img src="../../assets/images/activity/address.png" alt=""><span>{{list.museumName}}</span></div>

      <div class="vid" v-show="list.fAudio">
        <div class="audio green-audio-player">
          <div class="loading">
            <div class="spinner"></div>
          </div>


          <div class="controls">
            <span class="current-time">0:00</span>
            <div class="slider" data-direction="horizontal">
              <div class="ba"></div>
              <div class="progress">
                <div class="pin" id="progress-pin" data-method="rewind"></div>
              </div>
            </div>
            <span class="total-time">0:00</span>
          </div>
          <div class="play-pause-btn"></div>
          <div class="volume">
            <div class="volume-btn">
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                <path fill="#566574" fill-rule="evenodd" d="M14.667 0v2.747c3.853 1.146 6.666 4.72 6.666 8.946 0 4.227-2.813 7.787-6.666 8.934v2.76C20 22.173 24 17.4 24 11.693 24 5.987 20 1.213 14.667 0zM18 11.693c0-2.36-1.333-4.386-3.333-5.373v10.707c2-.947 3.333-2.987 3.333-5.334zm-18-4v8h5.333L12 22.36V1.027L5.333 7.693H0z" id="speaker"></path>
              </svg>
            </div>
            <div class="volume-controls hidden">
              <div class="slider" data-direction="vertical">
                <div class="progress">
                  <div class="pin" id="volume-pin" data-method="changeVolume"></div>
                </div>
              </div>
            </div>
          </div>

          <audio :src="list.fAudio" type="audio/mpeg"></audio>
        </div>
      </div>
      <div class="co">
        <div class="t1">藏品详情</div>
        <div class="coo">{{list.description}}</div>
      </div>
    </div>
  </div>

</template>
<script>
  import { Toast } from 'mint-ui';
  import $ from '../../../static/jquery.min'
  export default {
    name: "displayDetail",
    data(){
      return{
        id:"",
        list:"",
        type:"一级",
        img:"http://www.chnmuseum.cn/Portals/0/PictureNewsSettings/591/20161208-3636168000349044000.JPG",
        name:"叔五父铜匜",
        time:"西周",
        address:"天津博物馆西区一层B展厅",
        iscollected:1,
        zhonglei:"青铜器",
        cont:"展厅面积840平米，共展出馆藏古代青铜器、玉器、书画、瓷器、工艺等类别文物中最具代表性的艺术珍品63件套，通过对每一件珍品文物的深度诠释，凸显中华文明的悠久醇厚，更让观众从中真实感受古代中国人高度的艺术想象力和创造力以及天津深厚的文物集藏传统。 Selected Rare Relics Collected By Tianjin Museum （耀世奇珍——馆藏文物精品陈列）"
      }
    },
    methods:{
      chou(num,id){
        if(!this.$store.getters.userid) {
          this.$router.push({ path: '/my/login'});
          return;
        }
        if(num==1){
          this.$http.get("/m/myInfo/addCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:1}).then((res)=>{
            ;
            if(res.data.success==1){
              Toast('收藏成功');
              this.iscollected=1;
            } else if(res.data.success == 999) {
              this.$common.confirmLogin();
            } else{
              Toast('收藏失败');
            }
          })
        }else{
          this.$http.get("/m/myInfo/delCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:1}).then((res)=>{
            ;
            if(res.data.success==1){
              Toast('取消收藏成功');
              this.iscollected=0;
            } else if(res.data.success == 999) {
              this.$common.confirmLogin();
            } else{
              Toast('取消收藏失败');
            }
          })
        }
      },
      vid(){
        var vm = this;
        var audioPlayer = document.querySelector('.green-audio-player');
        var playPause = audioPlayer.querySelector('#playPause');
        var playpauseBtn = audioPlayer.querySelector('.play-pause-btn');
        var loading = audioPlayer.querySelector('.loading');
        var progress = audioPlayer.querySelector('.progress');
        var sliders = audioPlayer.querySelectorAll('.slider');
        var volumeBtn = audioPlayer.querySelector('.volume-btn');
        var volumeControls = audioPlayer.querySelector('.volume-controls');
        var volumeProgress = volumeControls.querySelector('.slider .progress');
        var player = audioPlayer.querySelector('audio');
        var currentTime = audioPlayer.querySelector('.current-time');
        var totalTime = audioPlayer.querySelector('.total-time');
        var speaker = audioPlayer.querySelector('#speaker');

        var draggableClasses = ['pin'];
        var currentlyDragged = null;

        window.addEventListener('mousedown', function (event) {

          if (!isDraggable(event.target)) return false;

          currentlyDragged = event.target;
          var handleMethod = currentlyDragged.dataset.method;

          this.addEventListener('mousemove', window[handleMethod], false);

          window.addEventListener('mouseup', function () {
            currentlyDragged = false;
            window.removeEventListener('mousemove', window[handleMethod], false);
          }, false);
        });

        playpauseBtn.addEventListener('click', togglePlay);
        player.addEventListener('timeupdate', updateProgress);
        player.addEventListener('volumechange', updateVolume);
        player.addEventListener('loadedmetadata', function () {
          totalTime.textContent = formatTime(player.duration);
        });
        player.addEventListener('canplay', makePlay);
        player.addEventListener('ended', function () {
          playPause.attributes.d.value = "M18 12L0 24V0";
          player.currentTime = 0;

        });

        volumeBtn.addEventListener('click', function () {
          volumeBtn.classList.toggle('open');
          volumeControls.classList.toggle('hidden');
        });

        window.addEventListener('resize', directionAware);

        sliders.forEach(function (slider) {
          var pin = slider.querySelector('.pin');
          slider.addEventListener('click', window[pin.dataset.method]);
        });

        directionAware();

        function isDraggable(el) {
          var canDrag = false;
          var classes = Array.from(el.classList);
          draggableClasses.forEach(function (draggable) {
            if (classes.indexOf(draggable) !== -1) canDrag = true;
          });
          return canDrag;
        }

        function inRange(event) {
          var rangeBox = getRangeBox(event);
          var rect = rangeBox.getBoundingClientRect();
          var direction = rangeBox.dataset.direction;
          if (direction == 'horizontal') {
            var min = rangeBox.offsetLeft;
            var max = min + rangeBox.offsetWidth;
            if (event.clientX < min || event.clientX > max) return false;
          } else {
            var min = rect.top;
            var max = min + rangeBox.offsetHeight;
            if (event.clientY < min || event.clientY > max) return false;
          }
          return true;
        }

        function updateProgress() {
          var current = player.currentTime;
          var percent = current / player.duration * 100;
          progress.style.width = percent + '%';

          currentTime.textContent = formatTime(current);
        }

        function updateVolume() {
          volumeProgress.style.height = player.volume * 100 + '%';
          if (player.volume >= 0.5) {
            speaker.attributes.d.value = 'M14.667 0v2.747c3.853 1.146 6.666 4.72 6.666 8.946 0 4.227-2.813 7.787-6.666 8.934v2.76C20 22.173 24 17.4 24 11.693 24 5.987 20 1.213 14.667 0zM18 11.693c0-2.36-1.333-4.386-3.333-5.373v10.707c2-.947 3.333-2.987 3.333-5.334zm-18-4v8h5.333L12 22.36V1.027L5.333 7.693H0z';
          } else if (player.volume < 0.5 && player.volume > 0.05) {
            speaker.attributes.d.value = 'M0 7.667v8h5.333L12 22.333V1L5.333 7.667M17.333 11.373C17.333 9.013 16 6.987 14 6v10.707c2-.947 3.333-2.987 3.333-5.334z';
          } else if (player.volume <= 0.05) {
            speaker.attributes.d.value = 'M0 7.667v8h5.333L12 22.333V1L5.333 7.667';
          }
        }

        function getRangeBox(event) {
          var rangeBox = event.target;
          var el = currentlyDragged;
          if (event.type == 'click' && isDraggable(event.target)) {
            rangeBox = event.target.parentElement.parentElement;
          }
          if (event.type == 'mousemove') {
            rangeBox = el.parentElement.parentElement;
          }
          return rangeBox;
        }

        function getCoefficient(event) {
          var slider = getRangeBox(event);
          var rect = slider.getBoundingClientRect();
          var K = 0;
          if (slider.dataset.direction == 'horizontal') {

            var offsetX = event.clientX - slider.offsetLeft;
            var width = slider.clientWidth;
            K = offsetX / width;
          } else if (slider.dataset.direction == 'vertical') {

            var height = slider.clientHeight;
            var offsetY = event.clientY - rect.top;
            K = 1 - offsetY / height;
          }
          return K;
        }

        function rewind(event) {
          if (inRange(event)) {
            player.currentTime = player.duration * getCoefficient(event);
          }
        }

        function changeVolume(event) {
          if (inRange(event)) {
            player.volume = getCoefficient(event);
          }
        }

        function formatTime(time) {
          var min = Math.floor(time / 60);
          var sec = Math.floor(time % 60);
          return min + ':' + (sec < 10 ? '0' + sec : sec);
        }

        function togglePlay() {
          if (player.paused) {
            $('.ba').addClass("active");
            $('.play-pause-btn').addClass("active");
            if(vm.$store.state.audio.audioshow){
              vm.$store.dispatch('isPause');
            }

            player.play();
          } else {
              vm.$store.dispatch('isPlay');
            player.pause();
            $('.ba').removeClass("active");
            $('.play-pause-btn').removeClass("active");
          }
        }

        function makePlay() {
          playpauseBtn.style.display = 'block';
          loading.style.display = 'none';
        }

        function directionAware() {
          if (window.innerHeight < 250) {
            volumeControls.style.bottom = '-54px';
            volumeControls.style.left = '54px';
          } else if (audioPlayer.offsetTop < 154) {
            volumeControls.style.bottom = '-164px';
            volumeControls.style.left = '-3px';
          } else {
            volumeControls.style.bottom = '52px';
            volumeControls.style.left = '-3px';
          }
        }
      }
    },
    mounted(){
      let _this=this;
      this.id=this.$route.query.id;
      this.$http.get("/m/collection/getCollectionById.do",{id:this.id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:3}).then((res)=>{
        if(res.data.success==1){
          this.list=res.data.data.collectionInfo;
          this.iscollected=res.data.data.collectionInfo.iscollection;
          this.$nextTick(()=>{
            _this.vid();
          })
        }else{
          Toast(res.data.data);
        }

      })

    }
  }
</script>

<style lang="less" scoped>
  .main{
    width: 100%;
    position: relative;
    .di{
      width: 100%;
      height: 18.75rem;
    }
    .shoucang{
      width:2.4rem;
      height:1.5rem;
      background:rgba(243,245,250,1);
      border-radius:0.25rem;
      position: absolute;
      top: 16rem;
      right: 1.25rem;
      font-size:0.7rem;
      font-weight:bold;
      color:rgba(91,102,160,1);
      line-height:1.5rem;
      text-align: center;
      &.active{
        background:rgba(72,165,255,1);
        color: #fff;
      }
    }
    .cont{
      width: 16.25rem;
      margin: 1rem auto 2rem;
      &.cur{

      }
      .ti{
        height: 1.05rem;
        span{
          &:nth-child(1){
            display: inline-block;
            width:2.3rem;
            height:1.05rem;
            background:rgba(72,165,255,1);
            border-radius:0.6rem;
            border:0.05rem solid;
            font-size:0.65rem;
            font-weight:400;
            color:rgba(255,255,255,1);
            line-height:1rem;
            text-align: center;
          }
          &:nth-child(2){
            display: inline-block;
            height:1.05rem;
            font-size:0.75rem;
            font-weight:bold;
            color:rgba(102,102,102,1);
            line-height:1.05rem;
            margin-left: 0.5rem;
          }
        }
      }
      .time{
        height: 1rem;
        margin-top: 1.05rem;
        img{
          width: 0.65rem;
          height: 0.65rem;
          vertical-align: top;
          margin-top: 0.15rem;
        }
        span{
          height:1rem;
          font-size:0.7rem;
          font-family:PingFangSC-Regular;
          font-weight:400;
          color:rgba(102,102,102,1);
          line-height:1rem;
          margin-left: 0.55rem;
          display: inline-block;
          vertical-align: top;
        }
      }
      .adress{
        height: 1rem;
        margin-top: 0.35rem;
        img{
          width: 0.65rem;
          height: 0.65rem;
          vertical-align: top;
          margin-top: 0.15rem;
        }
        span{
          height:1rem;
          font-size:0.7rem;
          font-family:PingFangSC-Regular;
          font-weight:400;
          color:rgba(102,102,102,1);
          line-height:1rem;
          margin-left: 0.55rem;
          display: inline-block;
          vertical-align: top;
        }
      }
      .zhonglei{
        height: 1rem;
        margin-top: 0.35rem;
        img{
          width: 0.65rem;
          height: 0.65rem;
          vertical-align: top;
          margin-top: 0.15rem;
        }
        span{
          height:1rem;
          font-size:0.7rem;
          font-family:PingFangSC-Regular;
          font-weight:400;
          color:rgba(102,102,102,1);
          line-height:1rem;
          margin-left: 0.55rem;
          display: inline-block;
          vertical-align: top;
        }
      }
      .vid{
        width:16.25rem;
        height:2rem;
        background:rgba(243,245,250,1);
        border-radius:1rem;
        margin-top: 1.85rem;
        margin-bottom: 1rem;
        background-size: 9.5rem 1rem;
        .audio.green-audio-player {
          width:16.25rem;
          height:2rem;
          border-radius:1rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding-right: 24px;
          user-select: none;
          -webkit-user-select: none;
          overflow: hidden;
          position: relative;
        }
        .audio.green-audio-player .play-pause-btn {
          display: none;
          cursor: pointer;
          position: absolute;
          top: -0.2rem;
          right: -0.2rem;
          background: url("../../assets/images/collect/v3.png") no-repeat center;
          background-size: 100% 100%;
          width: 2.4rem;
          height: 2.4rem;
          &.active{
            background: url("../../assets/images/collect/v4.png") no-repeat center;
          }
        }
        .current-time{
          width: 1.6rem;
          height: 1.6rem;
          background: #fff;
          line-height: 1.6rem;
          border-radius: 50%;
          font-size:0.6rem;
          font-weight:400;
          color:rgba(185,185,185,1);
          text-align: center;
          position: relative;
          z-index: 100;
        }
        .audio.green-audio-player .spinner {
          width: 18px;
          height: 18px;
          background-image: url(../../assets/images/collect/loading.png);
          background-size: cover;
          background-repeat: no-repeat;
          animation: spin 0.4s linear infinite;
        }
        .audio.green-audio-player .slider {
          flex-grow: 1;
          cursor: pointer;
          position: relative;
          margin-left: -2rem;
        }
        .ba{
          position: absolute;
          border-radius: 2rem;
          height: 2rem;
          width: 14.25rem !important;
          background:url("../../assets/images/collect/v1.png") no-repeat 3.5rem center;
          background-size: 9.5rem 1rem;
          z-index: 10;
          &.active{
            background:url("../../assets/images/collect/v2.gif") no-repeat 3.5rem center;
            background-size: 9.5rem 1rem;
          }
        }
        .audio.green-audio-player .slider .progress {
          background-color: #599bff;
          border-radius: inherit;
          position: absolute;
          pointer-events: none;
        }
        .audio.green-audio-player .slider .progress .pin {
          height: 16px;
          width: 16px;
          border-radius: 8px;
          background-color: #44BFA3;
          position: absolute;
          pointer-events: all;
          box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.32);
        }
        .audio.green-audio-player .controls {
          font-family: 'Roboto', sans-serif;
          font-size: 16px;
          line-height: 18px;
          color: #55606E;
          display: flex;
          flex-grow: 1;
          justify-content: space-between;
          align-items: center;
          margin-right: 24px;
          padding-left: 0.3rem;
        }
        .audio.green-audio-player .controls .slider {
          border-radius: 2rem;
          height: 2rem;
          width:16.25rem !important;
        }
        .audio.green-audio-player .controls .slider .progress {
          width: 0;
          height: 100%;
          border-radius: 2rem;
        }
        .audio.green-audio-player .controls .slider .progress .pin {
          right: -8px;
          top: -6px;
          display: none;
        }
        .audio.green-audio-player .controls span {
          cursor: default;
        }
        .audio.green-audio-player .volume {
          display: none;
          position: relative;
        }
        .audio.green-audio-player .volume .volume-btn {
          cursor: pointer;
        }
        .audio.green-audio-player .volume .volume-btn.open path {
          fill: #44BFA3;
        }
        .audio.green-audio-player .volume .volume-controls {
          width: 30px;
          height: 135px;
          background-color: rgba(0, 0, 0, 0.62);
          border-radius: 7px;
          position: absolute;
          left: -3px;
          bottom: 52px;
          flex-direction: column;
          align-items: center;
          display: flex;
        }
        .audio.green-audio-player .volume .volume-controls.hidden {
          display: none;
        }
        .audio.green-audio-player .volume .volume-controls .slider {
          margin-top: 12px;
          margin-bottom: 12px;
          width: 6px;
          border-radius: 3px;
        }
        .audio.green-audio-player .volume .volume-controls .slider .progress {
          bottom: 0;
          height: 100%;
          width: 6px;
        }
        .audio.green-audio-player .volume .volume-controls .slider .progress .pin {
          left: -5px;
          top: -8px;
        }
        @keyframes spin {
          from {
            transform: rotateZ(0);
          }
          to {
            transform: rotateZ(1turn);
          }
        }
      }
      .btn{
        margin-top: 0.85rem;
        .bt1{
          span{
            display: inline-block;
            width:7.75rem;
            height:1.5rem;
            background:rgba(243,245,250,1);
            border-radius:0.25rem;
            font-size:0.7rem;
            font-weight:bold;
            color:rgba(91,102,160,1);
            line-height:1.5rem;
            text-align: center;
            &:nth-child(2){
              margin-left: 0.75rem;
            }
          }
        }
        .bt2,.bt3{
          span{
            display: inline-block;
            width:16.25rem;
            height:1.5rem;
            background:rgba(243,245,250,1);
            border-radius:0.25rem;
            font-size:0.7rem;
            font-weight:bold;
            color:rgba(91,102,160,1);
            line-height:1.5rem;
            text-align: center;
          }
        }
      }
      .co{
        margin-top: 1rem;
        .t1{
          font-size:0.85rem;
          font-weight:bold;
          color:rgba(40,40,40,1);
          line-height:1.2rem;
        }
        .coo{
          margin-top: 1rem;
          font-size:0.7rem;
          font-weight:400;
          color:rgba(102,102,102,1);
          line-height:1rem;
        }
      }
    }
  }

</style>



