<template>
  <div class="page-home">
    <!-- 最新展览 start -->
    <div class="content-wrap content-wrap-exhibition">
      <div class="g-title" flex="dir:left main:justify cross:center">
        <h1>最新展览</h1>
        <router-link tag="span" to="/display" flex="dir:left cross:center">全部 {{dataExhibition.count}} <i class="g-icon g-icon-more"></i></router-link>
      </div>

      <div class="swiper-container slide-wrap-exhibition">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(item, index) in dataExhibition.listData" :key="index">
            <div class="slide-item">
              <img :src="item.pictureId" alt="" :onerror="defaultImg">
              <div class="slide-content">
                <p class="content ell">{{item.headline}}</p>
                <span class="des">{{item.orgName}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div v-if="dataExhibition.listData.length === 0">
        <div class="g-no-data" style="margin-top:0;">
          <div class="no-data-bg"></div>
          <p class="no-data-txt">暂无数据</p>
        </div>
      </div>
    </div>
    <!-- 最新展览 end -->

    <div class="content-wrap content-wrap-link" flex="dir:left main:justify cross:center">
      <router-link tag="div" to="/museumList" class="link-item link-left" flex="dir:left cross:center">
        <i class="g-icon icon-museum"></i>
        <div flex="dir:top">
          <p class="des">找博物馆</p>
          <span class="count">{{museumCount}}家</span>
        </div>
      </router-link>
      <router-link tag="div" to="/collect" class="link-item link-left" flex="dir:left cross:center">
        <i class="g-icon icon-collect"></i>
        <div flex="dir:top">
          <p class="des">查看藏品</p>
          <span class="count">{{countCollection}}件</span>
        </div>
      </router-link>
    </div>

    <!-- 最热活动 start -->
    <div class="content-wrap content-wrap-activity">
      <div class="g-title" flex="dir:left main:justify cross:center">
        <h1>最热活动</h1>
        <router-link tag="span" to="/activity" flex="dir:left cross:center">全部 {{dataActivity.count}} <i class="g-icon g-icon-more"></i></router-link>
      </div>
      
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <router-link tag="div" :to="{path: '/activityDetail', query: {id:item.activityId}}" class="swiper-slide slide-wrap-activity" v-for="(item, index) in dataActivity.listData" :key="index">
            <img :src="item.picUrl" alt="" :onerror="defaultImg">
            <p flex="dir:left main:justify">
              <span class="des ell">{{item.activityName}}</span>
              <span class="time">{{item.activityStartDate}}</span>
              <!-- <span class="time">{{item.activityTime}}</span> -->
            </p>
          </router-link>
        </div>

        <div class="swiper-pagination"></div>
      </div>

      <div v-if="dataActivity.listData.length === 0">
        <div class="g-no-data" style="margin-top:0;">
          <div class="no-data-bg"></div>
          <p class="no-data-txt">暂无数据</p>
        </div>
      </div>
    </div>
    <!-- 最热活动 end -->

    <!-- 文创产品 start -->
    <div class="content-wrap content-wrap-cultur">
      <div class="g-title" flex="dir:left main:justify cross:center">
        <h1>文创产品</h1>
        <!-- <router-link tag="span" to="/wenchuang" flex="dir:left cross:center">全部 {{dataCultur.count}} <i class="g-icon g-icon-more"></i></router-link> -->
      </div>

      <div class="list-cultur-wrap clearfix">
        <router-link tag="div" :to="{path: '/wenchuangDetail', query: {id:item.id}}" class="item-cultur" v-for="(item, index) in dataCultur.listData" :key="index">
          <img :src="item.pictureId" alt="" :onerror="defaultImg">
          <p class="des" flex="dir:left main:justify cross:center"><span>{{item.categoryName}}</span> <i class="g-icon icon-shop"></i></p>
          <p class="pay">¥{{item.price}}</p>
        </router-link>

        <div v-if="dataCultur.listData.length === 0">
          <div class="g-no-data">
            <div class="no-data-bg"></div>
            <p class="no-data-txt">暂无数据</p>
          </div>
        </div>
      </div>

      <router-link tag="div" to="/wenchuang" class="g-btn-more" v-if="dataCultur.listData.length > 0">查看更多</router-link>
    </div>
    <!-- 文创产品 end -->

    <!-- 资讯通告 start -->
    <div class="content-wrap content-warp-information">
      <div class="g-title" flex="dir:left main:justify cross:center">
        <h1>资讯通告</h1>
      </div>

      <ul class="list-information-wrap">
        <router-link tag="li" :to="{path: '/informationDetail', query: {id:item.id}}" class="item-information" v-for="(item, index) in dataInfoList" :key="index">
          <div class="item-top" flex="dir:left main:justify">
            <div class="item-top-left">
              <h2 class="two-ell">{{item.headline}}</h2>
              <p class="two-ell">{{item.content}}</p>
            </div>
            <img :src="item.picUrl" alt="" :onerror="defaultImg">
          </div>
          <div class="item-bottom" flex="dir:left main:justify cross:center">
            <span flex="dir:left cross:center">
              <img class="g-icon icon-info" :src="item.museumUrl" alt="" v-if="item.museumUrl">
              {{item.publisher}}
            </span>
            <span class="time">{{item.createTime}}</span>
          </div>
        </router-link>

        <div v-if="dataInfoList.length === 0">
          <div class="g-no-data">
            <div class="no-data-bg"></div>
            <p class="no-data-txt">暂无数据</p>
          </div>
        </div>
      </ul>

      <router-link tag="div" to="/information" class="g-btn-more" v-if="dataInfoList.length > 0">查看更多</router-link>
    </div>
    <!-- 资讯通告 end -->

    <cmp-footer :navType="1"></cmp-footer>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
import Swiper from 'swiper';
import 'swiper/dist/css/swiper.min.css';
import cmpFooter from '@cmp/footer'
export default {
  components: {
    cmpFooter
  },
  data() {
    return {
      defaultImg: this.$store.getters.defaultImg,
      isLogin: false,  // 用户是否登录
      museumCount: '',
      countCollection: '',
      dataExhibition: {
        count: '',
        listData: []
      },
      dataActivity: {
        count: '',
        listData: []
      },
      dataCultur: {
        count: '',
        listData: []
      },
      dataInfoList: []
    }
  },
  methods: {
    getData() {
      this.$http.get('/m/index/info.do', {

      }).then(response => {
        if(response.data.success == 1) {
          let data = response.data.data;
          this.dataExhibition.listData = data.newSpreadtrum;  // 最新展览
          this.dataActivity.listData = data.activityList.data;    // 最热活动
          this.dataCultur.listData = data.wenchuangList;  // 文创产品
          this.dataInfoList = data.informationList.data;    // 资讯通告

          this.dataExhibition.count = data.countSpreadtrum;  // 最新展览
          this.dataActivity.count = data.countActivity;    // 最热活动
          this.countCollection = data.countCollection;  // 藏品
          this.museumCount = data.museumCount;  // 博物馆

          this.initSwiper();
        } else {
          Toast({
            message: response.data.data,
            position: 'center'
          });
        }
      })
    },
    handleClickSlide(index) {
      this.$router.push({path: '/displayDetail', query: {id: this.dataExhibition.listData[index].id}});
    },
    initSwiper() {
      let vm = this;
      this.$nextTick(() => {
        var swiper = new Swiper('.content-wrap-exhibition .swiper-container', {
          effect: 'coverflow',
          loop: true,
          grabCursor: true,
          centeredSlides: true,
          slidesPerView: 'auto',
          coverflowEffect: {
            rotate: 50,
            stretch: 0,
            depth: 100,
            modifier: 1,
            slideShadows : true,
          },
          onClick: function() {
            vm.handleClickSlide(swiper.realIndex); 
          }
        });

        var swiperActivity = new Swiper('.content-wrap-activity .swiper-container', {
          autoplay: 5000,
          // loop: true,
          pagination: '.content-wrap-activity .swiper-pagination'
        });
      })
    }
  },
  mounted() {
    this.getData();
  }
}
</script>

<style lang="less" scoped>
  @import '../../assets/css/mixin.less';

  .page-home {
    padding: 1rem 0;
    .content-wrap {
      padding: 0 1rem;
      margin-bottom: 2rem;
    }
    .content-wrap-exhibition {
      padding: 0;
      .g-title {
        padding: 0 1rem;
      }
      .swiper-container {
        width: 100%;
        .swiper-slide {
          width: 300px;
          padding-bottom: .3rem;
        }
      }
      .slide-item {
        position: relative;
        width: 300px;
        // width: 16.25rem;
        box-shadow: 0 0.2rem 0.3rem #ccc;
        border-radius: .75rem;
        img {
          width: 100%;
          height: 200px;
          border-radius: .75rem .75rem 0 0;
          // width: 16.25rem;
          // height: 10.2rem;
        }
        .slide-content {
          padding: .5rem 1rem;
          .content {
            font-size: 0.75rem;
            line-height: 1.05rem;
            font-weight: bold;
            color: #666;
          }
          .des {
            margin-top: .25rem;
            color: #B9B9B9;
            font-size: .65rem;
            font-weight: 400;
            line-height: .9rem;
          }
        }
      }
    }
    .content-wrap-link {
      .link-item {
        .icon-museum {
          margin-right: .5rem;
          #f-bg(1.45rem, 1.4rem, "../images/home/icon-museum.png");
        }
        .icon-collect {
          margin-right: .5rem;
          #f-bg(1.45rem, 1.4rem, "../images/home/icon-collect.png");
        }
        .des {
          color: #4E4D4D;
          font-size: 1rem;
          font-weight: bold;
          line-height: 1.2rem;
        }
        .count {
          color: #4E4D4D;
          font-size: .6rem;
          font-weight: 400;
          line-height: .85rem;
        }
      }
    }
    .content-wrap-activity {
      .swiper-container {
        padding-bottom: .5rem;
        .swiper-pagination {
          position: absolute;
          bottom: 1.5rem;
        }
      }
      .slide-wrap-activity {
        width: 16.25rem;
        box-shadow: 0 0.2rem 0.3rem #ccc;
        border-radius: .75rem;
        img {
          width: 100%;
          height: 8.97rem;
          border-radius: 0.75rem 0.75rem 0 0;
        }
        p {
          padding: .5rem .5rem 2.7rem;
          .des {
            color: #666;
            font-size: .75rem;
            font-weight: bold;
            line-height: 1.05rem;
            width: 11rem;
            height: 1.05rem;
          }
          .time {
            color: #B9B9B9;
            font-size: .65rem;
            font-weight: 400;
            line-height: .9rem;
          }
        }
      }
    }
    .content-wrap-cultur {
      .list-cultur-wrap {
        margin-left: -1.25rem;
        margin-bottom: 1rem;
        .item-cultur {
          width: 7.5rem;
          margin-left: 1.25rem;
          margin-bottom: .5rem;
          float: left;
          img {
            width: 7.5rem;
            height: 7.25rem;
            border-radius: .25rem;
          }
          .des {
            margin-top: .5rem;
            color: #666;
            font-size: .75rem;
            font-weight: bold;
            line-height: 1.05rem;
            .icon-shop {
              #f-bg(.75rem, .65rem, "../images/home/icon-shop.png");
            }
          }
          .pay {
            margin-top: .3rem;
            color: #E94335;
            font-size: .6rem;
            font-weight: 400;
            line-height: .85rem;
          }
        }
      }
    }
    .content-warp-information {
      .list-information-wrap {
        .item-information {
          margin-bottom: 1.5rem;
          .item-top-left {
            width: 8.75rem;
            h2 {
              color: #666;
              font-size: .75rem;
              font-weight: bold;
              line-height: 1.05rem;
              height: 2.1rem;
            }
            p {
              margin-top: .6rem;
              color: #B9B9B9;
              font-size: .65rem;
              font-weight: 400;
              line-height: .9rem;
              height: 1.8rem;
            }
          }
          img {
            width:6rem;
            height:4.5rem;
            border-radius:0.25rem;
          }
        }
        .item-bottom {
          margin-top: .5rem;
          span {
            font-size: .65rem;
            color: #666;
            font-weight: bold;
            line-height: .9rem;
          }
          .icon-info {
            margin-right: .25rem;
            border-radius: 100%;
            #f-bg(.95rem, .95rem, "../images/home/icon-info.png");
          }
          .time {
            color: #B9B9B9;
            font-size: .65rem;
            font-weight: 400;
            line-height: .9rem;
          }
        }
      }
    }

  }
</style>

<style lang="less">
.page-home {
  .swiper-pagination {
    .swiper-pagination-bullet {
      width:0.5rem;
      height:0.2rem;
      background:rgba(216,216,216,1);
      border-radius:0.1rem;
    }
    .swiper-pagination-bullet-active {
      width:1.25rem;
      height:0.2rem;
      background:linear-gradient(54deg,rgba(128,226,255,1) 0%,rgba(72,165,255,1) 100%);
      border-radius:0.1rem;
    }
  }
  .swiper-container-3d .swiper-slide-shadow-bottom,
  .swiper-container-3d .swiper-slide-shadow-left,
  .swiper-container-3d .swiper-slide-shadow-right,
  .swiper-container-3d .swiper-slide-shadow-top {
    border-radius: 0.75rem;
  }
}
</style>






