<template>
  <mt-popup v-model="pp" position="left" closeOnClickModal="false">
    <div>
      <div class="tt">
        <img class="list" src="../assets/images/museum/list.png" alt="" @click="vis()">
      </div>
      <div class="co">
        <div class="co_left">
          <span v-for="(cu,index) in country" :class="{active:countryNum==index}" @click="quyu(index,cu.id,cu.name)">{{cu.name}}</span>
        </div>
        <div class="co_right">
          <div class="re"><img src="../assets/images/re.png" alt=""><span>{{name}}</span><i @click="quanqu()">进入全区 ></i></div>
          <div class="item_cont">
            <div class="item" @click="aa(muse.id)" v-for="muse in mu"><span>{{muse.name}}</span><img src="../assets/images/museum/next.png" alt=""></div>
          </div>
        </div>
      </div>
    </div>
  </mt-popup>
</template>

<script>
    export default {
        name: "popup",
      data(){
          return{
            country:[],
            countryNum:0,
            mu:[],
            pp:false,
            name:"",
            quyuId:""
          }
      },
      props:{
        text:Function,
        po:{type:Boolean,default:false},
      },
      watch:{
          po:function (newVal,oldVal) {
            this.pp=newVal;
          }
      },
      methods:{
        aa(name){
          this.pp=false;
          this.$parent.getData("",name);
        },
        vis(){
          this.pp=false;
          this.$parent.ch();
        },
        quanqu(){
          this.pp=false;
          this.$parent.getData(this.quyuId,"");
        },
        quyu(index,id,name){
          this.quyuId=id;
          this.name=name;
          this.countryNum=index;
          this.$http.get("/m/Guide/getMuseumSearchList.do",{hideLoadig: true,area:id,size:2000}).then((res)=>{
            this.mu=res.data.data;
          })
        }
      },
      mounted(){
          let _this=this;
         this.$http.get("/m/Guide/getAreaList.do", {hideLoadig: true}).then((res)=>{

           _this.country=res.data.data;
           _this.name=_this.country[0].name;
           _this.quyuId=_this.country[0].id;
           _this.$http.get("/m/Guide/getMuseumSearchList.do",{hideLoadig: true,area:_this.country[0].id,size:2000}).then((res1)=>{

             _this.mu=res1.data.data;
           })
         })
      }
    }
</script>

<style lang="less" scoped>
.tt{
  height: 3.5rem;
  line-height: 3.5rem;
  border-bottom:0.1rem solid #f3f3f3;
  img{
    margin-left: 1.25rem;
    height: 1.5rem;
  }
}
.co{
  width: 100%;
  position: absolute;
  top: 3.5rem;
  left: 0;
  bottom: 0;
  .co_left{
    background:rgba(243,245,250,1);
    float: left;
    height: 100%;
    width: 5rem;
    padding-top: 0.8rem;
    overflow-y: scroll;
    span{
      display: inline-block;
      height:1.1rem;
      font-size:0.65rem;
      line-height:1.1rem;
      margin: 0 0 2rem 1.25rem;
      font-weight:400;
      color:rgba(156,156,156,1);
      &.active{
        font-size: 0.8rem;
        font-weight:bold;
        color:rgba(74,74,74,1);
      }
    }
  }
  .co_right{
    position: absolute;
    top: 0;
    left: 5rem;
    bottom: 0;
    right: 0;
    overflow-y: scroll;
    .re{
      width: 100%;
      position: relative;
      img{
        width: 100%;
        height: 7.5rem;
      }
      span{
        display: block;
        position: absolute;
        font-size:1.5rem;
        font-weight:bold;
        color:rgba(255,255,255,1);
        top: 0.8rem;
        left: 1rem;
      }
      i{
        height:0.9rem;
        font-size:0.65rem;
        font-family:PingFangSC-Regular;
        font-weight:400;
        color:rgba(255,255,255,1);
        line-height:0.9rem;
        position: absolute;
        display: block;
        top: 5.85rem;
        right:1.55rem;
      }
    }
    .item_cont{
      position: absolute;
      top: 7.5rem;
      bottom: 0;
      right: 0;
      left: 0;
      overflow-y: scroll;
      .item{
        margin: 0.95rem 0 0 1rem;
        border-bottom: 0.1rem solid #f3f3f3;
        padding-bottom: 0.95rem;
        span{
          display: inline-block;
          height:1rem;
          font-size:0.7rem;
          color:rgba(74,74,74,1);
          line-height:1rem;
          vertical-align: top;
          letter-spacing: 1px;
        }
        img{
          float: right;
          margin-top: 0.2rem;
          margin-right: 1.25rem;
        }
      }
    }
  }
}

</style>
<style>
  .main .mint-popup{
    position: fixed;
    width: 100%;
    height: 100%;
    border-radius: 0;
  }
</style>



