<template>
  <div class="main">
    <img class="di" :src="data.pictureId" alt="">
    <div class="cont">
      <div class="ti"><span>{{data.productName}}</span></div>

      <div class="adress"><img src="../../assets/images/display/address.png" alt=""><span>{{data.nickName}}</span></div>
      <div class="btn">
        <p class="bt3"><a :href="data.productNumber" target="_blank">购买</a></p>
      </div>
      <div class="co">
        <div class="t1">文创详情</div>
        <div class="coo">{{data.designElements}}</div>
      </div>
    </div>
  </div>

</template>
<script>
  export default {
    name: "displayDetail",
    data(){
      return{
        data:"",
        id:"",
        }
    },
    methods:{

    },
    mounted(){
      this.id=this.name=this.$route.query.id;
      this.$http.get("/m/wenChuang/getWenchuang.do",{id:this.id}).then((res)=>{

        this.data=res.data.data;
      })
    }
  }
</script>

<style lang="less" scoped>
  .main{
    width: 100%;
    .di{
      width: 100%;
      height: 14.05rem;
    }
    .cont{
      width: 16.25rem;
      margin: 1rem auto 2rem;
      .ti{
        height: 1.05rem;
        span{
          display: inline-block;
          height:1.05rem;
          font-size:0.75rem;
          font-weight:bold;
          color:rgba(102,102,102,1);
          line-height:1.05rem;
        }
      }
      .adress{
        height: 1rem;
        margin-top: 1.05rem;
        img{
          width: 0.65rem;
          height: 0.65rem;
          vertical-align: top;
          margin-top: 0.15rem;
        }
        span{
          height:1rem;
          font-size:0.7rem;
          font-weight:400;
          color:rgba(102,102,102,1);
          line-height:1rem;
          margin-left: 0.55rem;
          display: inline-block;
          vertical-align: top;
        }
      }
      .btn{
        margin-top: 0.85rem;
        .bt3{
          a{
            display: inline-block;
            width:16.25rem;
            height:1.5rem;
            background:rgba(243,245,250,1);
            border-radius:0.25rem;
            font-size:0.7rem;
            font-weight:bold;
            color:rgba(91,102,160,1);
            line-height:1.5rem;
            text-align: center;
          }
        }
      }
      .co{
        margin-top: 1rem;
        .t1{
          font-size:0.85rem;
          font-weight:bold;
          color:rgba(40,40,40,1);
          line-height:1.2rem;
        }
        .coo{
          margin-top: 1rem;
          font-size:0.7rem;
          font-weight:400;
          color:rgba(102,102,102,1);
          line-height:1rem;
        }
      }
    }
  }
</style>



