<template>
  <div class="main">
    <div class="title">
      <img class="list" src="../../assets/images/museum/list.png" alt="" @click="we()">
      <input ref="lk" type="text" placeholder="输入展览名称" maxlength="10" v-model="key" @keyup.enter="ss">
      <span><i :class="{active:tab}" ref="type0" @click="choose(0)">全部</i> | <i :class="{active:!tab}" ref="type1" @click="choose(1)">火热</i></span>
      <div class="aa" @click="ss()"></div>
      <div class="fenlei"><span :class="{active:al==1}" @click="toggle(1)">{{type}}</span><span :class="{active:al==2}" @click="toggle(2)">{{year}}</span></div>
      <div class="fenleiList" :class="{active:al==1}">
        <div class="swiper-container">
          <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="(ty,index) in typeList">
              <i ref="type" @click="typeS(ty.id,index,ty.name)" :class="{active:typeNum==index}">{{ty.name}}</i>
            </div>
          </div>
        </div>
      </div>
      <div class="fenleiList" :class="{active:al==2}">
        <div class="swiper-container">
          <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="(ye,index) in yearList">
              <i ref="year" @click="yearS(ye.value,index,ye.name)" :class="{active:yearNum==index}">{{ye.name}}</i>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="cont" :class="{active:al>0,cur:$store.state.audio.audioshow}">
      <div class="chenlie">
        <div class="line"></div>
        <div class="cont_wrap">
          <div class="item" v-for="(di,index) in chenlie">
            <p>{{di.name}}  <i></i></p>
            <router-link :to="{path:'/collectDetail',query:{id:di.id}}"><img class="dis" :src="di.pics[0].url" alt=""></router-link>
          </div>
        </div>
        <div class="more" @click="more()" v-if="chenlie.length != 0">查看更多</div>
      </div>
      <div v-if="chenlie.length === 0">
        <div class="g-no-data" style="margin-top:0;">
          <div class="no-data-bg"></div>
          <p class="no-data-txt">暂无数据</p>
        </div>
      </div>
    </div>
    <div class="black_wrap" v-show="al>0" :class="{cur:$store.state.audio.audioshow}"></div>
    <pop :po="popp"></pop>
  </div>
</template>

<script>
  import pop from '../../components/popup';
  import Swiper from 'swiper';
  import 'swiper/dist/css/swiper.min.css';
  import { Toast } from 'mint-ui';
  export default {
    name: "index",
    data(){
      return{
        pageNum:1,
        totalNum:0,
        chenlie:[],
        tab:true,
        al:0,
        typeList:[],
        yearList:[{"name":"全部","value":''},{"name":"中华人民共和国","value":97},{"name":"民国","value":96},{"name":"清","value":95},{"name":"明","value":94},{"name":"元","value":93},{"name":"宋","value":87},{"name":"唐","value":79},{"name":"东汉","value":57},{"name":"西汉","value":56},{"name":"汉","value":55},{"name":"东周","value":51},{"name":"西周","value":50},{"name":"周","value":49},{"name":"商","value":48},{"name":"新石器时代","value":45}],
        type:"分类",
        year:"年代",
        typeValue:"",
        yearValue:"",
        typeNum:'',
        yearNum:'',
        key:"",
        popp:false,
        quyuId:"",
        museumId:"",
        isHighQuality:""
      }
    },
    components:{
      pop
    },
    methods:{
      ss(){
        this.key=this.$refs.lk.value;
        this.getData();
      },
      ch(){
        this.popp=false;
      },
      we(){
        if(this.popp){
          this.popp=false;
        }else{
          this.popp=true;
        }
      },
      getData(quyuId,museumId){
        this.popp=false;
        this.pageNum=1;

        this.quyuId=quyuId;
        this.museumId=museumId;
        this.$http.get("/m/collection/getCollectionList.do",{
          userid: this.$store.getters.userid,
          size: 10,
          currentPage: this.pageNum,
          collectionsCategory:this.typeValue,
          year:this.yearValue,
          city:this.quyuId,
          museumId:this.museumId,
          name:this.key,
          isHighQuality:this.isHighQuality
        }).then((res)=>{
          this.chenlie=res.data.data;
          this.totalNum=res.data.page.totalPage;
        })
      },
      choose(num){
        if(num==0){
          this.tab=true;
          this.isHighQuality="";
        }else{
          this.isHighQuality=1;
          this.tab=false;
        }
        this.getData();
      },
      more(){
        if(this.pageNum==this.totalNum){
          Toast('已全部加载');
          return false;
        }else{
          this.pageNum++;
          this.$http.get("/m/collection/getCollectionList.do",{
            userid: this.$store.getters.userid,
            size: 10,
            currentPage: this.pageNum,
            collectionsCategory:this.typeValue,
            year:this.yearValue,
            city:this.quyuId,
            museumId:this.museumId,
            name:this.key,
            isHighQuality:this.isHighQuality
          }).then((res)=>{
            this.chenlie=this.chenlie.concat(res.data.data);
          })
        }
      },
      toggle(num){
        if(this.al==num){
          this.al=0;
          return false;
        }else{
          this.al=num;
        }
      },
      typeS(type,index,name){
        if(this.typeNum==index){
          this.type="分类";
          this.typeNum=-1;
          this.typeValue="";
        }else{
          this.typeValue=type;
          this.type=name;
          this.typeNum=index;
        }
        this.al=0;
        this.getData();
      },
      yearS(year,index,name){
        if(this.yearNum==index){
          this.year="年代";
          this.yearNum=-1;
          this.yearValue="";
        }else{
          this.yearValue=year;
          this.year=name;
          this.yearNum=index;
        }
        this.al=0;
        this.getData();
      }
    },
    mounted(){
      this.getData();
      this.$http.get("/m/collection/getCollectionSearch.do").then((res)=>{
        ;
        this.typeList=res.data.data;
        this.typeList.unshift({
          id: '',
          name: '全部'
        });
        this.$nextTick(()=>{
          var swiper = new Swiper('.swiper-container', {
            slidesPerView: 'auto',
          });
        })
      })

    }
  }
</script>

<style lang="less" scoped>

  .main {
    width: 100%;
    .title {
      margin-top: 1px;
      height: 4.9rem;
      padding-top: 1rem;
      padding-left: 1.25rem;
      background: #fff;
      position: relative;
      .list {
        height: 1.5rem;
      }
      input {
        width: 9.85rem;
        height: 1.5rem;
        border-radius: 0.75rem;
        vertical-align: top;
        margin-left: 0.8rem;
        font-size: 0.6rem;
        font-weight: 400;
        color: rgba(185, 185, 185, 1);
        line-height: 1.5rem;
        padding-left: 1.8rem;
        background: rgb(241, 241, 241) url("../../assets/images/museum/sousuo.png") no-repeat 0.45rem 0.3rem;
      }
      span {
        display: inline-block;
        margin-left: 0.5rem;
        font-size: 0.65rem;
        color: rgba(185, 185, 185, 1);
        font-weight: 400;
        vertical-align: top;
        height: 1.5rem;
        line-height: 1.5rem;
        i {
          vertical-align: top;
          &.active {
            color: rgba(72, 165, 255, 1);
          }
        }
      }
      .aa{
        position: absolute;
        width: 1.3rem;
        height: 1.3rem;
        top: 1.25rem;
        left:4rem;
        z-index: 100;
      }
      .fenlei{
        width: 11.6rem;
        margin: 0.75rem auto 0;
        span{
          display: inline-block;
          width: auto;
          padding-right: 1rem;
          height:0.9rem;
          font-size:0.65rem;
          font-weight:400;
          color:rgba(102,102,102,1);
          line-height:0.9rem;
          margin-left: 0;
          background: url("../../assets/images/collect/shang.png") no-repeat right 0.25rem;
          background-size: 0.43rem 0.43rem;
          &:nth-child(2){
            float: right;
          }
          &.active{
            color:rgba(72,165,255,1);
            background: url("../../assets/images/collect/xia.png") no-repeat right 0.25rem;
            background-size: 0.43rem 0.43rem;
          }
        }
      }
      .fenleiList{
        left: 0;
        height: 0;
        overflow-y: hidden;
        background:rgba(246,246,246,1);
        position: absolute;
        top: 4.9rem;
        z-index: 1000;
        width: 100%;
        transition: all 0.5s;
        &.active{
          height:1.9rem;
        }
        .swiper-slide{
          width: auto;
          display: -webkit-box;
          display: -ms-flexbox;
          display: -webkit-flex;
          display: flex;
          i{
            display: block;
            height:1.15rem;
            border-radius:0.57rem;
            font-size:0.65rem;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(102,102,102,1);
            line-height:1.15rem;
            margin: 0.4rem 0 0.35rem 1.25rem;
            padding: 0 0.5rem;
            text-align: center;
            &.active{
              background:rgba(72,165,255,1);
              color: #fff;
            }
          }
        }
      }
    }
    .line{
      height: 0.5rem;
      background: rgba(241, 241, 241, 1);
    }
    .cont{
      width: 100%;
      background-color: #fff;
      position: absolute;
      top: 4.9rem;
      bottom: 0;
      overflow-y: scroll;
      &.cur{
        top:7.9rem
      }
      &.active{
        overflow-y: hidden;
      }
      .chenlie{
        .cont_wrap{
          width: 16.25rem;
          margin: 0 auto;
          padding-top: 1rem;
          &:after{
            content: '';
            display: block;
            clear: both;
          }
          .ti{
            font-size:0.85rem;
            font-weight:bold;
            color:rgba(40,40,40,1);
            margin-bottom: 1rem;
          }
          .item{
            margin-bottom: 2rem;
            width: 7.5rem;
            float: left;
            margin-right: 1.25rem;
            &:nth-child(2n){
              margin-right: 0;
            }
            .dis{
              width:7.5rem;
              height:7.5rem;
              border-radius:0.25rem;
            }
            p{
              height:1.05rem;
              font-size:0.75rem;
              font-weight:bold;
              color:rgba(102,102,102,1);
              line-height:1.05rem;
              margin-bottom: 0.5rem;
              overflow:hidden; //超出的文本隐藏
              text-overflow:ellipsis; //溢出用省略号显示
              white-space:nowrap; //溢出不换行
            }
          }
        }
        .more{
          width:16.25rem;
          height:1.5rem;
          background:rgba(243,245,250,1);
          border-radius:0.25rem;
          font-size:0.65rem;
          text-align: center;
          font-weight:bold;
          color:rgba(91,102,160,1);
          line-height:1.5rem;
          margin:0 auto 1.3rem;
          touch-action: none;
        }
      }
    }
    .black_wrap{
      background-color: rgba(0,0,0,0.3);
      position: absolute;
      width: 100%;
      top: 4.9rem;
      bottom: 0;
      left:0;
      right: 0;
      &.cur{
        top:7.9rem
      }
    }
  }
</style>



