<template>
  <div class="page-register">
    <div class="register" flex="dir:top">
      <div class="item-wrap item-phone" flex="dir:left box:mean">
        <input type="password" placeholder="请输入旧密码" v-model="oldPass">
      </div>
      <div class="item-wrap item-phone" flex="dir:left box:mean">
        <input type="password" placeholder="请输入新密码 (6至10位字母或数字)" v-model="newPass">
      </div>
      <div class="item-wrap item-phone" flex="dir:left box:mean">
        <input type="password" placeholder="请再次输入新密码" v-model="newPassAgain" @keyup.enter="save">
      </div>
    </div>
    <div flex="dir:left main:center">
      <a class="submit" href="javascript:;" :class="{enable: submitEnable}" @click="save">确认</a>
    </div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
import { validate } from '@/utils/util';
export default {
  data() {
    return {
      oldPass: '',
      newPass: '',
      newPassAgain: ''
    }
  },
  computed: {
    // 注册按钮是否可用
    submitEnable() {
      return this.oldPass && this.newPass && this.newPassAgain;
    }
  },
  methods: {
    save() {
      if(!this.submitEnable) { return; }

      // 验证原密码
      this.$http.get('/m/myInfo/checkPassword.do', {
        userID: this.$store.getters.userid,
        token: this.$store.getters.token,
        password: this.oldPass
      }).then(res => {
        if(res.data.success == 1) {
          this.modify();
        } else if(res.data.success == 999) {
          this.$common.confirmLogin();
        } else {
          Toast({
            message: res.data.data || '接口异常',
            position: 'bottom'
          });
        }
      });
    },
    // 修改密码
    modify() {
      if(!validate(this.newPass, 'password')) {
        Toast({
          message: '请设置6至10位登录密码，至少包含一位数字和字母',
          position: 'bottom'
        });
        return;
      } 
      if(this.newPass !== this.newPassAgain) {
        Toast({
          message: '两次密码不统一，请重新输入',
          position: 'bottom'
        });
        return;
      } 
      if(this.newPass === this.oldPass) {
        Toast({
          message: '新密码和旧密码不能相同',
          position: 'bottom'
        });
        return;
      } 
      this.$http.get('/m/myInfo/modifyPassword.do', {
        userID: this.$store.getters.userid,
        token: this.$store.getters.token,
        password: this.newPass
      }).then(res => {
        if(res.data.success == 1) {
          let instance = Toast({
            message: '修改成功！',
            position: 'center',
            duration: 2000,
            iconClass: 'icon icon-success'
          });
          setTimeout(() => {
            instance.close();
            this.$router.push({ path: '/my'});
          }, 2000);
        } else {
          Toast({
            message: res.data.data || '接口异常',
            position: 'bottom'
          });
        }
      });
    }
  }
}
</script>

<style lang="less" scoped>
  @import '../../assets/css/mixin.less';
  .page-register {
    padding: 5rem 2.25rem;
    font-size: 16px;
    .register {
      .item-wrap {
        // font-size: 16px;
        position: relative;
        padding: 0 1rem;
        height:2.2rem;
        background:rgba(241,241,241,1);
        border-radius: 1.1rem;
        margin-bottom: 1rem;
        input {
          display: inline-block;
          // font-size: 14px;
          // font-size: .65rem;
          line-height: 1.2rem;
          background: transparent;
          color: #666;
          &::-webkit-input-placeholder {
            color: #B9B9B9;
          }
          &:-moz-placeholder {
            color: #B9B9B9;
          }
          &:-ms-input-placeholder {
            color: #B9B9B9;
          }
        }
      }
    }
    .submit {
      width: 100%;
      text-align: center;
      margin-top: .8rem;
      height:2.2rem;
      line-height: 2.2rem;
      background: #999;
      border-radius:1.1rem;
      color: #fff;
      font-size: 18px;
      // font-size: .8rem;
      &.enable {
        background:linear-gradient(126deg,rgba(72,165,255,1) 0%,rgba(129,226,255,1) 100%);
      }
    }
  }
</style>





