<template>
  <div class="my-exhibition">
    <div class="my-wrap-content">
      <!-- tab切换 start-->
      <cmp-mynav :navType="3"></cmp-mynav>
      <!-- tab切换 end-->

      <div class="list-wrap">
        <div class="list-box">
          <div class="list-item" v-for="(item, index) in listData" data-type="0" :key="index">
            <div flex="dir:left box:first" @touchstart.capture="touchStart" @touchmove.capture="touchMove" @click="skip($event,item.cid)">
              <div class="item-pic">
                <img class="item-pic" :src="item.picUrl" alt="" :onerror="defaultImg">
                <span class="item-label">{{item.exhibitionType === '1' ? '常设展览' : '临时展览'}}</span>
              </div>
              <div class="item-wrap">
                <h2 class="my-title two-ell">{{item.headLine}}<i class="g-icon icon-radio" v-if="item.isListen==='1'"></i></h2>
                <div class="my-des">
                  <p class="loc" flex="dir:left cross:center">{{item.orgName}}</p>
                  <p flex="dir:left cross:center">{{item.showTimeStr}}</p>
                </div>
              </div>
            </div>
            <div class="delete" flex="cross:center" @click="deleteItem" :data-index="index" :data-id="item.cid">删除</div>
          </div>

          <div v-if="listData.length === 0">
            <div class="g-no-data">
              <div class="no-data-bg"></div>
              <p class="no-data-txt">暂无数据</p>
            </div>
            <router-link tag="a" to="/display" class="my-btn-collect">去收藏</router-link>
          </div>
        </div>

        <div class="my-more" @click="more()" v-if="!isTotal && listData.length>0">查看更多</div>
      </div>
    </div>
  </div>
</template>

<script>
import cmpMynav from '@cmp/myNav'
import { MessageBox } from 'mint-ui'
import { utilSlide } from '@/utils/util'
export default {
  components: {
    cmpMynav
  },
  data() {
    return {
      defaultImg: this.$store.getters.defaultImg,
      currentPage: 1,
      pageSize: 10,
      isTotal: true,  // 是否加载全部
      listData: []
    }
  },
  methods: {
    //跳转
    skip(obj,id){
      if( utilSlide.checkSlide() ){
        utilSlide.restSlide();
      } else {
        this.$router.push({ path: '/displayDetail', query: { id: id }});
      }
    },
    //滑动开始
    touchStart(e){
      utilSlide.touchStart(e);
    },
    //滑动
    touchMove(e){
      utilSlide.touchMove(e);
    },
    //删除
    deleteItem(e){
      let id = e.currentTarget.dataset.id;
      this.$emit('emitDeleCollect', {
        userID: this.$store.getters.userid,
        token: this.$store.getters.token,
        collectID: id
      });
    },
    // 查看更多
    more() {
      this.currentPage++;
      this.$emit('emitMore', {
        userID: this.$store.getters.userid,
        token: this.$store.getters.token,
        type: 3,  // 1藏品 2博物馆 3展览 4活动
        currentPage: this.currentPage,
        size: this.pageSize,
        openPage: 1  // 是否开启分页
      });
    },
    // 获取列表数据
    getData() {
      this.$emit('emitGetData', {
        userID: this.$store.getters.userid,
        token: this.$store.getters.token,
        type: 3,  // 1藏品 2博物馆 3展览 4活动
        currentPage: this.currentPage,
        size: this.pageSize,
        openPage: 1  // 是否开启分页
      });
    }
  },
  mounted() {
    this.getData();
  },
}
</script>

<style lang="less" scoped>
  @import '../../assets/css/mixin.less';
  @import './my-common.less';
  .m-my {
    .my-exhibition {
      .list-box {
        .list-item {
          margin-bottom: 1.15rem;
          .item-pic {
            width: 6rem;
            height: 4.5rem;
            margin-right: 1rem;
            position: relative;
            &::after {
              content: '';
              width: 100%;
              height: .9rem;
              display: block;
              // background: rgba(0, 0, 0, 0.3);
              position: absolute;
              left: 0;
              bottom: 0;
              border-radius: 0 0 0.25rem 0.25rem;
            }
            img {
              width: 6rem;
              height: 4.5rem;
            }
            .item-label {
              position: absolute;
              left: 0;
              bottom: 0;
              padding: 0 .2rem;
              color: #fff;
              background: #48A5FF;
              border-radius:0rem 0.25rem 0rem 0.25rem;
              font-size: .65rem;
              line-height: .9rem;
              z-index: 2;
              &::after {
                content: '';
                display: block;
                border-style: solid;
                border-width: .2rem 0 0 .3rem;
                border-color: transparent transparent transparent #48A5FF;
                position: absolute;
                right: -.27rem;
                bottom: 0;
              }
            }
          }
          .my-title {
            height: 2rem;
            margin-bottom: .65rem;
            padding-right: 1rem;
          }
          .my-des {
            .loc {
              color: #666;
              margin-bottom: .2rem;
            }
          }
        }
      }
    }
  }
</style>





