<template>
  <div class="page-my-info">
    <div class="top">
      <img :src="avatarurl" alt="" :onerror="defaultHeadImg" @click="clickFile">
    </div>

    <div class="form-wrap">
      <div class="form-item" flex="dir:left main:justify box:last cross:center">
        <div flex="dir:left box:first cross:center">
          <span class="name">用户名</span>
          <input class="des" type="text" v-model="username" maxlength="10">
        </div>
        <div>
          <i class="g-icon g-icon-more"></i>
        </div>
      </div>

      <div class="form-item" flex="dir:left main:justify box:last cross:center">
        <div flex="dir:left box:first cross:center">
          <span class="name">性别</span>
          <div @click="sexVisible = true">{{sex}}</div>
        </div>
        <div>
          <i class="g-icon g-icon-more"></i>
        </div>
      </div>

      <div class="form-item" @click="openPicker" flex="dir:left main:justify box:last cross:center">
        <div flex="dir:left box:first cross:center">
          <span class="name">生日</span>
          <div class="des">{{birthday}}</div>
        </div>
        <div>
          <i class="g-icon g-icon-more"></i>
        </div>
      </div>

      <div class="form-item" flex="dir:left main:justify box:last cross:center">
        <div flex="dir:left box:first cross:center">
          <span class="name">职业</span>
          <input class="des" type="text" v-model="job" maxlength="10" placeholder="最多输入10字">
        </div>
        <div>
          <i class="g-icon g-icon-more"></i>
        </div>
      </div>

      <div class="form-item" flex="dir:left main:justify box:last cross:top">
        <div flex="dir:left box:first cross:top">
          <span class="name">个性签名</span>
          <textarea name="" id="" rows="2" v-model="des" maxlength="20" placeholder="最多输入20字"></textarea>
        </div>
        <div>
          <i class="g-icon g-icon-more"></i>
        </div>
      </div>

      <div flex="dir:left main:center">
        <a class="form-submit enable" href="javascript:;" @click="save">确认</a>
      </div>

    </div>

    <!-- 性别弹出层 start-->
    <div class="sexPicker">
      <mt-popup
        v-model="sexVisible"
        position="bottom">
        <mt-picker :slots="slots" @change="onSexChange" value-key="name"></mt-picker>
      </mt-popup>
    </div>
    <!-- 性别弹出层 end -->

    <!-- 日期弹出层 start-->
    <div class="datePicker">
      <mt-datetime-picker
        v-model="mytime"
        type="date"
        ref="picker"
        year-format="{value} 年"
        month-format="{value} 月"
        date-format="{value} 日"
        @confirm="getBirthdayVal"
        :startDate="startDate">
      </mt-datetime-picker>
    </div>
    <!-- 日期弹出层 end-->

    <input type="file" id="fileInput" style="display: none;" @change="triggerFile($event)" ref="filePicker">
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
import moment from 'moment'
export default {
  data() {
    return {
      defaultHeadImg: this.$store.getters.defaultHeadImg,
      avatarurl: '',  // 头像
      mytime: new Date(),
      username: 'vivi',
      birthday:"",  //出生日期
      job: '',
      des: '',  // 个性签名
      startDate: new Date('1968-01-01'),
      sex: '男',
      sexId: '0',
      sexVisible: false,  //选择器的显示与影藏
      slots: [
        {
          flex: 1,
          values: [{"id":"0","name":'男'},{"id":"1","name":'女'}],
          className: 'slot1',
          textAlign: 'center',
          defaultIndex: 0
        }
      ]
    }
  },
  methods: {
    clickFile() {
      // document.getElementById('fileInput').click();
      const event = new MouseEvent('click');
      this.$refs.filePicker.dispatchEvent(event);
    },
    triggerFile(e) {
      let vm = this;
      let deviceFile = e.target.files;
      let formData = new FormData();

      formData.append('file', deviceFile[0]);
      formData.append('userId', this.$store.getters.userid);
      formData.append('token', this.$store.getters.token);

      let config = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }

      this.$http.base({
        method: 'post',
        url: '/m/uploadHeadImage.do',
        headers: {'Content-Type': 'multipart/form-data'},
        data: formData
      }).then((response)=>{
        if(response.data.success==1) {
          vm.avatarurl = response.data.data;
        } else if (response.data.success==999) {
          this.$common.confirmLogin();
        } else {
          Toast({
            message: response.data.data || '接口异常',
            position: 'bottom'
          });
        }
      });
    },
    // 性别
    openPicker() {
      this.$refs.picker.open();
    },
    onSexChange(picker, values) {
      this.slots[0].value = values[0].name;
      this.slots[0].valueKey = values[0].id;
      this.sex = values[0].name;
      this.sexId = values[0].id;
      this.sexVisible = false;
    },
    // 生日
    getBirthdayVal(data) {
      this.birthday = moment(data).format('YYYY/MM/DD');
    },
    save() {
      this.$http.get('/m/myInfo/saveInfo.do', {
        id: this.$store.getters.userid,
        token: this.$store.getters.token,
        nickName: this.username,
        sex: this.sexId,
        birthday: this.birthday,
        job: this.job,
        tags: '',
        description: this.des
      }).then(response => {

        if(response.data.success == 1) {
          let instance = Toast({
            message: '保存成功！',
            position: 'center',
            iconClass: 'icon icon-success'
          });
          setTimeout(() => {
            instance.close();
            this.$router.push({ path: '/my'});
          }, 2000);
        } else if(response.data.success == 999){
          this.$common.confirmLogin();
        } else {
          Toast({
            message: '保存失败！',
            position: 'center',
            duration: 2000,
            iconClass: 'icon icon-fail'
          });
        }
      }, error => {
        console.log('异常');
      })
    },
    getData() {
      this.$http.get('/m/myInfo/userInfo.do', {
        userID: this.$store.getters.userid,
        token: this.$store.getters.token
      }).then(response => {

        if(response.data.success == 1) {
          let data = response.data.data.user;

          this.avatarurl = data.avatarurl;
          this.username = data.nickName;
          this.sex = data.sex == 0 ? '男' : '女';
          this.slots[0].defaultIndex = data.sex == 0 ? 0 : 1;  // 设置性别默认值
          this.birthday = data.birth;
          this.job = data.job;
          this.des = data.description;
        } else {
          this.$router.push({ path: '/my/login'});
        }
      }, error => {
        console.log('异常');
      })
    }
  },
  mounted() {
    this.getData();
  }
}
</script>

<style lang="less" scoped>
  .page-my-info {
    font-size: 16px;
    .top {
      width: 5rem;
      margin: auto;
      padding: 1.5rem 0;
      img {
        width: 5rem;
        height: 5rem;
        border-radius: 100%;
      }
    }

    .form-wrap {
      padding: 0 1.25rem;
      // font-size: .7rem;
      font-weight: 500;
      color: #666;
      .form-item {
        line-height: 2.5rem;
        border-bottom: solid 1px #F3F3F3;
        input {
          display: inline-block;
          font-size: 16px;
          line-height: 1.3rem;
        }
        textarea {
          padding-top: .63rem;
          padding-bottom: .63rem;
          // font-size: .7rem;
          line-height: 1.3rem;
        }
        .name {
          display: inline-block;
          width: 3.8rem;
        }
        .des {
          // font-size: .7rem;
          // color: #282828;
          // font-weight: bold;
        }
      }
      .form-submit {
        width: 100%;
        text-align: center;
        margin-top: 1.8rem;
        height:2.2rem;
        line-height: 2.2rem;
        background: #999;
        border-radius:1.1rem;
        color: #fff;
        font-size: 18px;
        // font-size: .8rem;
        &.enable {
          background:linear-gradient(126deg,rgba(72,165,255,1) 0%,rgba(129,226,255,1) 100%);
        }
      }
    }
  }
</style>





