<template>
  <div class="my-nav-warp" flex="dir:left box:mean" :class="{cur:show}">
    <router-link tag="span" to="/my" :class="['nav-item', navType==1?'active':'']">我的博物馆</router-link>
    <router-link tag="span" to="/my/collect" :class="['nav-item', navType==2?'active':'']">我的藏品</router-link>
    <router-link tag="span" to="/my/exhibition" :class="['nav-item', navType==3?'active':'']">我的展览</router-link>
    <router-link tag="span" to="/my/activity" :class="['nav-item', navType==4?'active':'']">我的活动</router-link>
  </div>
</template>

<script>
export default {
  name: 'cmpMynav',
  props: {
    navType: {
      type: Number,
      default: '1'
    }
  },
  data() {
    return {
        show:false
    }
  },
  computed: {
    getUserIcons() {
      return this.$store.state.audio.audioshow
    }
  },
  watch: {
    getUserIcons(val) {
      this.show = val;
    }
  },
  mounted(){
   if(this.$store.state.audio.audioshow){
     this.show=true
   }else{
     this.show=false
    }
  },

}
</script>

<style lang="less" scoped>
  .my-nav-warp {
    font-size: .65rem;
    font-weight: 400;
    color: #B9B9B9;
    line-height: .9rem;
    position: fixed;
    width: 100%;
    background: #fff;
    padding: 0.5rem 1.25rem 0.5rem 0;
    border-radius: 0.75rem 0.75rem 0rem 0rem;
    z-index: 2;
    &.cur{
      top: 14.1rem!important;
    }
    .nav-item {
      position: relative;
      text-align: center;
      &.active {
        color: #48A5FF;
        &::after {
          content: "";
          width: .86rem;
          height: .1rem;
          border-radius: .05rem;
          background: #48A5FF;
          position: absolute;
          left: 50%;
          bottom: -3px;
          margin-left: -.43rem;
        }
      }
    }
  }
</style>




