<template>
    <div class="main">
      <img class="di" :src="data.pictureId" alt="">
      <div class="shoucang" v-show="iscollected==0" @click="chou(1,data.id)">收藏</div>
      <div class="shoucang active" v-show="iscollected==1" @click="chou(2,data.id)">已收藏</div>
      <div class="cont">
        <div class="ti"><span v-show="type">{{type}}</span><span>{{data.headline}}</span></div>
        <div class="time"><img src="../../assets/images/display/time.png" alt=""><span>{{data.openTime}}</span></div>
        <div class="adress"><img src="../../assets/images/display/address.png" alt=""><span>{{data.openAddress}}</span></div>
        <div class="btn">
          <p class="bt1" v-show="mp3 && xuni"><span @click="gotopage">进入收听</span><span @click="open">虚拟展厅</span></p>
          <p class="bt2" v-show="mp3 && !xuni"><span @click="gotopage">进入收听</span></p>
          <p class="bt3" v-show="!mp3 && xuni"><span @click="open">虚拟展厅</span></p>
        </div>
        <div class="co">
          <div class="t1">展览详情</div>
          <div class="coo" v-html="data.content"></div>
        </div>
      </div>
    </div>

</template>
<script>
  import { Toast } from 'mint-ui';
    export default {
        name: "displayDetail",
      data(){
          return{
            data:"",
            type:"",
            iscollected:1,
            mp3:false,
            xuni:false,
            }
      },
      methods:{
        open(){
          window.open(this.data.viMoveUrl)
        },
        gotopage(){
          this.$router.push({
            path:"/GuideDeteil",
            query:{id:this.data.tourId }
          })
        },
          chou(num,id){
            if(!this.$store.getters.userid) {
              this.$router.push({ path: '/my/login'});
              return;
            }
            if(num==1){
              this.$http.get("/m/myInfo/addCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:3}).then((res)=>{
                if(res.data.success==1){
                  Toast('收藏成功');
                  this.iscollected=1;
                } else if(res.data.success == 999) {
                  this.$common.confirmLogin();
                } else{
                  Toast('收藏失败');
                }
              })
            }else{
              this.$http.get("/m/myInfo/delCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:3}).then((res)=>{
                ;
                if(res.data.success==1){
                  Toast('取消收藏成功');
                  this.iscollected=0;
                } else if(res.data.success == 999) {
                  this.$common.confirmLogin();
                } else{
                  Toast('取消收藏失败');
                }
              })
            }
          }
      },
      mounted(){
        this.id=this.$route.query.id;
        this.$http.get("/m/spreadtrum/getSpreadtrumById.do",{id:this.id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:2}).then((res)=>{
          ;
          this.data=res.data.data.Spreadtrum;
          this.mp3=this.data.tourId;
          // this.xuni=this.data.haveHall;
          this.xuni=this.data.viMoveUrl;
          this.iscollected=res.data.data.Spreadtrum.isCollection
          this.type=res.data.data.Spreadtrum.exhibitionType==='1'?"常设展览":"临时展览";
        })
      }
    }
</script>

<style lang="less" scoped>
.main{
  width: 100%;
  position: relative;
  .di{
    width: 100%;
    height: 14.05rem;
  }
  .shoucang{
    width:2.4rem;
    height:1.5rem;
    background:rgba(243,245,250,1);
    border-radius:0.25rem;
    position: absolute;
    top: 11.3rem;
    right: 1.25rem;
    font-size:0.7rem;
    font-weight:bold;
    color:rgba(91,102,160,1);
    line-height:1.5rem;
    text-align: center;
    &.active{
      background:rgba(72,165,255,1);
      color: #fff;
    }
  }
  .cont{
    width: 16.25rem;
    margin: 1rem auto 2rem;
    .ti{
      height: 1.05rem;
      span{
        &:nth-child(1){
          display: inline-block;
          width:3.55rem;
          height:1.05rem;
          background:rgba(72,165,255,1);
          border-radius:0.6rem;
          border:0.05rem solid;
          font-size:0.65rem;
          font-weight:400;
          color:rgba(255,255,255,1);
          line-height:1.05rem;
          text-align: center;
          vertical-align: top;
        }
        &:nth-child(2){
          display: inline-block;
          height:1.05rem;
          font-size:0.75rem;
          font-weight:bold;
          color:rgba(102,102,102,1);
          line-height:1.05rem;
          margin-left: 0.5rem;
          width: 12rem;
          overflow: hidden;
          text-overflow:ellipsis;
          white-space: nowrap;
          vertical-align: top;
        }
      }
    }
    .time{
      height: 1rem;
      margin-top: 1.05rem;
      img{
        width: 0.65rem;
        height: 0.65rem;
        vertical-align: top;
        margin-top: 0.15rem;
      }
      span{
        height:1rem;
        font-size:0.7rem;
        font-family:PingFangSC-Regular;
        font-weight:400;
        color:rgba(102,102,102,1);
        line-height:1rem;
        margin-left: 0.55rem;
        display: inline-block;
        vertical-align: top;
      }
    }
    .adress{
      height: 1rem;
      margin-top: 0.35rem;
      img{
        width: 0.65rem;
        height: 0.65rem;
        vertical-align: top;
        margin-top: 0.15rem;
      }
      span{
        height:1rem;
        font-size:0.7rem;
        font-family:PingFangSC-Regular;
        font-weight:400;
        color:rgba(102,102,102,1);
        line-height:1rem;
        margin-left: 0.55rem;
        display: inline-block;
        vertical-align: top;
      }
    }
    .btn{
      margin-top: 0.85rem;
      .bt1{
        span{
          display: inline-block;
          width:7.75rem;
          height:1.5rem;
          background:rgba(243,245,250,1);
          border-radius:0.25rem;
          font-size:0.7rem;
          font-weight:bold;
          color:rgba(91,102,160,1);
          line-height:1.5rem;
          text-align: center;
          &:nth-child(2){
            margin-left: 0.75rem;
          }
        }
      }
      .bt2,.bt3{
        span{
          display: inline-block;
          width:16.25rem;
          height:1.5rem;
          background:rgba(243,245,250,1);
          border-radius:0.25rem;
          font-size:0.7rem;
          font-weight:bold;
          color:rgba(91,102,160,1);
          line-height:1.5rem;
          text-align: center;
        }
      }
    }
    .co{
      margin-top: 1rem;
      .t1{
        font-size:0.85rem;
        font-weight:bold;
        color:rgba(40,40,40,1);
        line-height:1.2rem;
      }
      .coo{
        margin-top: 1rem;
        font-size:0.7rem;
        font-weight:400;
        color:rgba(102,102,102,1);
        line-height:1rem;
      }
    }
  }
}
</style>
<style>
  .coo p img{
    max-width: 100%;
  }
</style>



