<template>
  <div class="main">
    <img class="di" :src="data.picUrl" alt="">
    <div class="shoucang"v-show="iscollected==0"  @click="chou(1,data.activityId)">收藏</div>
    <div class="shoucang active" v-show="iscollected==1" @click="chou(2,data.activityId)">已收藏</div>
    <div class="cont">
      <div class="ti"><span>{{data.activityName}}</span></div>
      <div class="time"><img src="../../assets/images/activity/time.png" alt=""><span>{{data.activityTime}}</span></div>
      <div class="adress"><img src="../../assets/images/activity/address.png" alt=""><span>{{data.activityPlace}}</span></div>
      <div class="co">
        <div class="t1">活动详情</div>
        <div class="coo" v-html="data.content"></div>
      </div>
    </div>
  </div>

</template>
<script>
  import { MessageBox, Toast } from 'mint-ui';
  export default {
    name: "displayDetail",
    data(){
      return{
        id:"",
        type:"",
        data:"",
        iscollected:"",
      }
    },
    methods:{
      chou(num,id){
        if(!this.$store.getters.userid) {
          this.$router.push({ path: '/my/login'});
          return;
        }
        if(num==1){

          this.$http.get("/m/myInfo/addCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:4}).then((res)=>{
            ;
            if(res.data.success==1){
              Toast('收藏成功');
              this.iscollected=1;
            } else if(res.data.success == 999) {
              this.$common.confirmLogin();
            } else{
              Toast('收藏失败');
            }
          })
        }else{
          this.$http.get("/m/myInfo/delCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:4}).then((res)=>{
            ;
            if(res.data.success==1){
              Toast('取消收藏成功');
              this.iscollected=0;
            } else if(res.data.success == 999) {
              this.$common.confirmLogin();
            } else{
              Toast('取消收藏失败');
            }
          })
        }
      }
    },
    mounted(){
      this.id=this.$route.query.id;
      this.type=this.$route.query.type;
      this.$http.get("/m/activity/getActivityById.do",{activityId:this.id,uniqueName:this.type,userID: this.$store.getters.userid, token: this.$store.getters.token}).then((res)=>{
        ;
        this.data=res.data.data;
        this.iscollected=res.data.data.isCollection;
      })
    }
  }
</script>

<style lang="less" scoped>
  .main{
    width: 100%;
    position: relative;
    .di{
      width: 100%;
      height: 14.05rem;
    }
    .shoucang{
      width:2.4rem;
      height:1.5rem;
      background:rgba(243,245,250,1);
      border-radius:0.25rem;
      position: absolute;
      top: 11.3rem;
      right: 1.25rem;
      font-size:0.7rem;
      font-weight:bold;
      color:rgba(91,102,160,1);
      line-height:1.5rem;
      text-align: center;
      &.active{
        background:rgba(72,165,255,1);
        color: #fff;
      }
    }
    .cont{
      width: 16.25rem;
      margin: 1rem auto 2rem;
      .ti{
        span{
          display: inline-block;
          font-size:0.75rem;
          font-weight:bold;
          color:rgba(102,102,102,1);
          line-height:1.05rem;
        }
      }
      .time{
        height: 1rem;
        margin-top: 1.05rem;
        img{
          width: 0.65rem;
          height: 0.65rem;
          vertical-align: top;
          margin-top: 0.15rem;
        }
        span{
          height:1rem;
          font-size:0.7rem;
          font-family:PingFangSC-Regular;
          font-weight:400;
          color:rgba(102,102,102,1);
          line-height:1rem;
          margin-left: 0.55rem;
          display: inline-block;
          vertical-align: top;
        }
      }
      .adress{
        height: 1rem;
        margin-top: 0.35rem;
        img{
          width: 0.65rem;
          height: 0.65rem;
          vertical-align: top;
          margin-top: 0.15rem;
        }
        span{
          height:1rem;
          font-size:0.7rem;
          font-family:PingFangSC-Regular;
          font-weight:400;
          color:rgba(102,102,102,1);
          line-height:1rem;
          margin-left: 0.55rem;
          display: inline-block;
          vertical-align: top;
        }
      }
      .btn{
        margin-top: 0.85rem;
        .bt1{
          span{
            display: inline-block;
            width:7.75rem;
            height:1.5rem;
            background:rgba(243,245,250,1);
            border-radius:0.25rem;
            font-size:0.7rem;
            font-weight:bold;
            color:rgba(91,102,160,1);
            line-height:1.5rem;
            text-align: center;
            &:nth-child(2){
              margin-left: 0.75rem;
            }
          }
        }
        .bt2,.bt3{
          span{
            display: inline-block;
            width:16.25rem;
            height:1.5rem;
            background:rgba(243,245,250,1);
            border-radius:0.25rem;
            font-size:0.7rem;
            font-weight:bold;
            color:rgba(91,102,160,1);
            line-height:1.5rem;
            text-align: center;
          }
        }
      }
      .co{
        margin-top: 1rem;
        .t1{
          font-size:0.85rem;
          font-weight:bold;
          color:rgba(40,40,40,1);
          line-height:1.2rem;
        }
        .coo{
          margin-top: 1rem;
          font-size:0.7rem;
          font-weight:400;
          color:rgba(102,102,102,1);
          line-height:1rem;
        }
      }
    }
  }
</style>



