<template>
  <div class="main">
    <div class="title">
      <img class="list" src="../../assets/images/museum/list.png" alt="" @click="we()">
      <input ref="lk" type="text" placeholder="输入展览名称" maxlength="10">
      <span><i :class="{active:tab}" ref="type0" @click="choose(0)">临时展览</i> | <i :class="{active:!tab}" ref="type1" @click="choose(1)">展厅陈列</i></span>
      <div @click="ss()"></div>
    </div>
    <div class="cont">
      <div class="linshi" v-show="tab">
        <div class="line"></div>
        <div class="cont_wrap">
          <div class="ti">正在展出</div>
          <div class="item" v-for="(di,index) in linshi.showList">
            <router-link :to="{path:'/displayDetail',query:{id:di.id}}"><img class="dis" :src="di.pictureId" alt=""></router-link>
            <p><span>{{di.headline}}</span><i :class="{active:di.isCollection==1}" ref="list" @click="shoucang(index,di.id)"></i></p>
            <div><img src="../../assets/images/display/icon.png" alt=""><span>{{di.openAddress}}</span><i>{{di.openTime}}</i></div>
          </div>
          <div v-if="linshi.showList&&linshi.showList.length === 0">
            <div class="g-no-data" style="margin-top:0;">
              <div class="no-data-bg"></div>
              <p class="no-data-txt">暂无数据</p>
            </div>
          </div>
        </div>
        <div class="line"></div>
        <div class="sw">
          <div class="ti">已结束</div>
          <div class="swiper-container">
            <div class="swiper-wrapper">
              <div class="swiper-slide" v-for="(di,index) in linshi.overList">
                <router-link :to="{path:'/displayDetail',query:{id:di.id}}"><img :src="di.pictureId" alt=""></router-link>
                <p><span>{{di.headline}}</span></p>
                <div class="mu">{{di.openAddress}}<i :class="{active:di.isCollection==1}" ref="list1" @click="shoucang1(index,di.id)"></i></div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="chenlie" v-show="!tab">
        <div class="line"></div>
        <div class="cont_wrap">
          <div class="item" v-for="(di,index) in linshi.showList">
            <router-link :to="{path:'/displayDetail',query:{id:di.id}}"><img class="dis" :src="di.pictureId" alt=""></router-link>
            <p>{{di.headline}}  <i :class="{active:di.isCollection==1}" ref="list2" @click="shoucang2(index,di.id)"></i></p>
            <div><img src="../../assets/images/display/icon.png" alt=""><span>{{di.openAddress}}</span><i>{{di.openTime}}</i></div>
          </div>
          <div class="more" @click="more()" v-if="linshi.showList&&linshi.showList.length != 0">查看更多</div>
        </div>
        <div v-if="linshi.showList&&linshi.showList.length === 0">
          <div class="g-no-data" style="margin-top:0;">
            <div class="no-data-bg"></div>
            <p class="no-data-txt">暂无数据</p>
          </div>
        </div>
      </div>
    </div>
    <pop :po="popp"></pop>
  </div>
</template>

<script>
  import Swiper from 'swiper';
  import 'swiper/dist/css/swiper.min.css';
  import { Toast } from 'mint-ui';
  import pop from '../../components/popup';
    export default {
        name: "index",
      data(){
          return{
            tab:true,
            linshi:[],   //临时展览
            pageNum:1,
            totalNum:1,
            type:"0",
            key:"",
            popp:false,
            quyuId:"",
            museumId:"",
            size:8
          }
      },
      components:{
          pop
      },
      methods:{
        ss(){
          this.key=this.$refs.lk.value;
          var that=this;
          this.$http.get("/m/spreadtrum/getSpreadtrumList.do",{area:this.quyuId,museumId:this.museumId,key:this.key,type:this.type,userId: this.$store.getters.userid, token: this.$store.getters.token}).then((res)=>{
            ;
            this.linshi=res.data.data;
          })
        },
        ch(){
          this.popp=false;
        },
        we(){
          if(this.popp){
            this.popp=false;
          }else{
            this.popp=true;
          }
        },
        getData(quyuId,museumId){
          this.popp=false;
          this.pageNum=1;
          this.quyuId=quyuId;
          this.museumId=museumId;
          this.$http.get("/m/spreadtrum/getSpreadtrumList.do",{currentPage:this.pageNum,area:this.quyuId,museumId:this.museumId,key:this.key,type:this.type,userId: this.$store.getters.userid, token: this.$store.getters.token}).then((res)=>{
            ;
            this.linshi=res.data.data;
          })
        },
        choose(num){
          if(num==0){
            this.tab=true;
            this.type=0;
            this.museumId=""
          }else{
            this.tab=false;
            this.type=1;
            this.pageNum=1;
            this.museumId=""
          }
          this.$http.get("/m/spreadtrum/getSpreadtrumList.do",{currentPage:this.pageNum,area:this.quyuId,museumId:this.museumId,key:this.key,type:this.type,userId: this.$store.getters.userid, token: this.$store.getters.token}).then((res)=>{
            this.linshi=res.data.data;
            this.totalNum=res.data.page.totalPage;
          })
        },
        shoucang(index,id){
          if(!this.$store.getters.userid) {
            this.$router.push({ path: '/my/login'});
            return;
          }
          if(this.$refs.list[index].classList.contains("active")==false){
            this.$http.get("/m/myInfo/addCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:3}).then((res)=>{
              ;
              if(res.data.success==1){
                this.$refs.list[index].classList.add("active");
                Toast('收藏成功');
              } else if(res.data.success == 999) {
                this.$common.confirmLogin();
              } else{
                Toast('收藏失败');
              }
            })
          }else{
            this.$http.get("/m/myInfo/delCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:3}).then((res)=>{
              ;
              if(res.data.success==1){
                this.$refs.list[index].classList.remove("active");
                Toast('取消收藏成功');
              } else if(res.data.success == 999) {
                this.$common.confirmLogin();
              } else{
                Toast('取消收藏失败');
              }
            })
          }
        },
        shoucang1(index,id){
          if(!this.$store.getters.userid) {
            this.$router.push({ path: '/my/login'});
            return;
          }
          if(this.$refs.list1[index].classList.contains("active")==false){
            this.$http.get("/m/myInfo/addCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:3}).then((res)=>{
              ;
              if(res.data.success==1){
                this.$refs.list1[index].classList.add("active");
                Toast('收藏成功');
              } else if(res.data.success == 999) {
                this.$common.confirmLogin();
              } else{
                Toast('收藏失败');
              }
            })
          }else{
            this.$http.get("/m/myInfo/delCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:3}).then((res)=>{
              ;
              if(res.data.success==1){
                this.$refs.list1[index].classList.remove("active");
                Toast('取消收藏成功');
              } else if(res.data.success == 999) {
                this.$common.confirmLogin();
              } else{
                Toast('取消收藏失败');
              }
            })
          }
        },
        shoucang2(index,id){
          if(!this.$store.getters.userid) {
            this.$router.push({ path: '/my/login'});
            return;
          }
          if(this.$refs.list2[index].classList.contains("active")==false){

            this.$http.get("/m/myInfo/addCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:3}).then((res)=>{
              ;
              if(res.data.success==1){
                this.$refs.list2[index].classList.add("active");
                Toast('收藏成功');
              } else if(res.data.success == 999) {
                this.$common.confirmLogin();
              } else{
                Toast('收藏失败');
              }
            })
          }else{
            this.$http.get("/m/myInfo/delCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:3}).then((res)=>{
              ;
              if(res.data.success==1){
                this.$refs.list2[index].classList.remove("active");
                Toast('取消收藏成功');
              } else if(res.data.success == 999) {
                this.$common.confirmLogin();
              } else{
                Toast('取消收藏失败');
              }
            })
          }
        },
        more(){
          if(this.pageNum==this.totalNum){
            Toast('已全部加载');
            return false;
          }else{
            this.pageNum++;
            this.$http.get("/m/spreadtrum/getSpreadtrumList.do",{currentPage:this.pageNum,area:this.quyuId,museumId:this.museumId,key:this.key,type:this.type,userId: this.$store.getters.userid, token: this.$store.getters.token}).then((res)=>{
              ;
              this.linshi.showList=this.linshi.showList.concat(res.data.data.showList);
            })
          }
        }
      },
      mounted(){
        this.$http.get("/m/spreadtrum/getSpreadtrumList.do",{currentPage:this.pageNum,size:this.size,type:this.type,area:this.quyuId,museumId:this.museumId,userId: this.$store.getters.userid, token: this.$store.getters.token}).then((res)=>{
          ;
          this.linshi=res.data.data;
          this.$nextTick(()=>{
            var swiper = new Swiper('.swiper-container', {
              slidesPerView: 'auto',
            });
          })
        })


      }
    }
</script>

