          }
          .item{
            margin-bottom: 2rem;
            .dis{
              width: 16.25rem;
              height: 12.2rem;
              border-radius: 8px;
            }
            p{
              height:1rem;
              font-size:0.75rem;
              font-family:PingFang-SC-Bold;
              font-weight:bold;
              color:rgba(102,102,102,1);
              line-height:1.05rem;
