<template>
  <div class="my-museum">
    <div class="my-wrap-content">
      <!-- tab切换 start-->
      <cmp-mynav :navType="1"></cmp-mynav>
      <!-- tab切换 end-->
      
      <div class="list-wrap">
        <div class="list-box">
          <div class="list-item" v-for="(item, index) in listData" data-type="0" :key="index">
            <div flex="dir:left box:first" @touchstart.capture="touchStart" @touchmove.capture="touchMove" @click="skip(item.cid)">
              <img class="item-pic" :src="item.picUrl" alt="" :onerror="defaultImg">
              <div class="item-wrap" flex="dir:top">
                <h2 class="my-title ell">{{item.orgName}}</h2>
                <div class="my-des">
                  <p class="loc" flex="dir:left cross:center"><i class="g-icon icon-loc"></i><span class="ell">{{item.address}}</span></p>
                  <p flex="dir:left cross:center"><i class="g-icon icon-dir"></i>{{item.meter}}</p>
                </div>
              </div>
            </div>
            <div class="delete" flex="cross:center" @click="deleteItem" :data-index="index" :data-id="item.cid">删除</div>
          </div>

          <div v-if="listData.length === 0">
            <div class="g-no-data">
              <div class="no-data-bg"></div>
              <p class="no-data-txt">暂无数据</p>
            </div>
            <router-link tag="a" to="/museumList" class="my-btn-collect">去收藏</router-link>
          </div>

        </div>

        <div class="my-more" @click="more()" v-if="!isTotal && listData.length>0">查看更多</div>
      </div>

      <div id="allmap"></div>
    </div>
  </div>
</template>

<script>
import cmpMynav from '@cmp/myNav'
import { Indicator, Toast } from 'mint-ui'
import { utilSlide } from '@/utils/util'
export default {
  components: {
    cmpMynav
  },
  data() {
    return {
      latitude: '',
      longitude: '',
      defaultImg: this.$store.getters.defaultImg,
      currentPage: 1,
      pageSize: 10,
      isTotal: true,  // 是否加载全部
      listData: []
    }
  },
  methods: {
    //跳转
    skip(id){
      if( utilSlide.checkSlide() ){
        utilSlide.restSlide();
      } else {
        this.$router.push({ path: '/museumDetail', query: { id: id }});
      }
    },
    //滑动开始
    touchStart(e){
      utilSlide.touchStart(e);
    },
    //滑动
    touchMove(e){
      utilSlide.touchMove(e);
    },
    //删除
    deleteItem(e){
      let id = e.currentTarget.dataset.id;
      this.$emit('emitDeleCollect', {
        userID: this.$store.getters.userid,
        token: this.$store.getters.token,
        collectID: id
      });
    },
    // 查看更多
    more() {
      this.currentPage++;
      this.$emit('emitMore', {
        latitude: this.latitude,
        longitude: this.longitude,
        userID: this.$store.getters.userid,
        token: this.$store.getters.token,
        type: 2,  // 1藏品 2博物馆 3展览 4活动
        currentPage: this.currentPage,
        size: this.pageSize,
        openPage: 1  // 是否开启分页
      });
    },
    // 获取列表数据
    getData() {
      this.$emit('emitGetData', {
        latitude: this.latitude,
        longitude: this.longitude,
        userID: this.$store.getters.userid,
        token: this.$store.getters.token,
        type: 2,  // 1藏品 2博物馆 3展览 4活动
        currentPage: this.currentPage,
        size: this.pageSize,
        openPage: 1  // 是否开启分页
      });
    },
    // 获取经纬度
    getLatitude() {
      let vm = this;
      var geolocation = new BMap.Geolocation();
      Indicator.open({spinnerType: 'fading-circle'});
      geolocation.getCurrentPosition(function(r){
        Indicator.close();
        if(this.getStatus() == BMAP_STATUS_SUCCESS){
          vm.latitude = r.point.lat;
          vm.longitude = r.point.lng;
          vm.getData();
        } else {
          console.log('failed'+this.getStatus());
        }        
      }, function() {
        Indicator.close();
      }, {enableHighAccuracy: true});
    }
  },
  mounted() {
    this.getLatitude();
  },
}
</script>

<style lang="less" scoped>
  @import '../../assets/css/mixin.less';
  @import './my-common.less';
</style>





