<template>
  <div class="main">
    <img class="di" :src="data.picUrl" alt="">

    <div class="cont">
      <div class="ti"><span>{{data.headline}}</span></div>
      <div class="time"><img src="../../assets/images/activity/time.png" alt=""><span>{{data.createTime}}</span></div>
      <div class="adress"><img src="../../assets/images/activity/address.png" alt=""><span>{{data.publisher}}</span></div>
      <div class="co">
        <div class="t1">咨询详情</div>
        <div class="coo" v-html="data.content"></div>
      </div>
    </div>
  </div>

</template>
<script>
  export default {
    name: "displayDetail",
    data(){
      return{
        id:"",
        type:"",
        data:""
      }
    },
    methods:{
      chou(num){
        if(num==1){
          this.iscollected=1;
        }else{
          this.iscollected=0;
        }
      }
    },
    mounted(){
      this.id=this.$route.query.id;
      this.type=this.$route.query.type==0?"xwdt":"tzgg";
      this.$http.get("/m/article/getArticleById.do",{id:this.id,uniqueName:this.type}).then((res)=>{
        ;
        this.data=res.data.data;
      })
    }
  }
</script>

<style lang="less" scoped>
  .main{
    width: 100%;
    .di{
      width: 100%;
      height: 14.05rem;
    }
    .shoucang{
      width:2.4rem;
      height:1.5rem;
      background:rgba(243,245,250,1);
      border-radius:0.25rem;
      position: absolute;
      top: 11.3rem;
      right: 1.25rem;
      font-size:0.7rem;
      font-weight:bold;
      color:rgba(91,102,160,1);
      line-height:1.5rem;
      text-align: center;
      &.active{
        background:rgba(72,165,255,1);
        color: #fff;
      }
    }
    .cont{
      width: 16.25rem;
      margin: 1rem auto 2rem;
      .ti{
        span{
          display: inline-block;
          font-size:0.75rem;
          font-weight:bold;
          color:rgba(102,102,102,1);
          line-height:1.05rem;
        }
      }
      .time{
        height: 1rem;
        margin-top: 1.05rem;
        img{
          width: 0.65rem;
          height: 0.65rem;
          vertical-align: top;
          margin-top: 0.15rem;
        }
        span{
          height:1rem;
          font-size:0.7rem;
          font-family:PingFangSC-Regular;
          font-weight:400;
          color:rgba(102,102,102,1);
          line-height:1rem;
          margin-left: 0.55rem;
          display: inline-block;
          vertical-align: top;
        }
      }
      .adress{
        height: 1rem;
        margin-top: 0.35rem;
        img{
          width: 0.65rem;
          height: 0.65rem;
          vertical-align: top;
          margin-top: 0.15rem;
        }
        span{
          height:1rem;
          font-size:0.7rem;
          font-family:PingFangSC-Regular;
          font-weight:400;
          color:rgba(102,102,102,1);
          line-height:1rem;
          margin-left: 0.55rem;
          display: inline-block;
          vertical-align: top;
        }
      }
      .btn{
        margin-top: 0.85rem;
        .bt1{
          span{
            display: inline-block;
            width:7.75rem;
            height:1.5rem;
            background:rgba(243,245,250,1);
            border-radius:0.25rem;
            font-size:0.7rem;
            font-weight:bold;
            color:rgba(91,102,160,1);
            line-height:1.5rem;
            text-align: center;
            &:nth-child(2){
              margin-left: 0.75rem;
            }
          }
        }
        .bt2,.bt3{
          span{
            display: inline-block;
            width:16.25rem;
            height:1.5rem;
            background:rgba(243,245,250,1);
            border-radius:0.25rem;
            font-size:0.7rem;
            font-weight:bold;
            color:rgba(91,102,160,1);
            line-height:1.5rem;
            text-align: center;
          }
        }
      }
      .co{
        margin-top: 1rem;
        .t1{
          font-size:0.85rem;
          font-weight:bold;
          color:rgba(40,40,40,1);
          line-height:1.2rem;
        }
        .coo{
          margin-top: 1rem;
          font-size:0.7rem;
          font-weight:400;
          color:rgba(102,102,102,1);
          line-height:1rem;
        }
      }
    }
  }
</style>



