<template>
    <div class="main">
      <div class="title">
        <img class="list" src="../../assets/images/museum/list.png" alt="" @click="we()">
        <input ref="lk" type="text" placeholder="输入博物馆名称" v-model="key" @keyup.enter="ss">
        <span><i class="active" ref="type0" @click="choose(0)">推荐</i> | <i ref="type1" @click="choose(1)">距离</i> | <i ref="type2" @click="choose(2)">火热</i></span>
        <div @click="ss()"></div>
      </div>
      <div class="line"></div>
      <div class="cont" :class="{cur:$store.state.audio.audioshow}">
        <div class="cont_wrap">
          <div class="item" v-for="(it,index) in data">
            <div class="ti">{{it.name}} <i :class="{active:it.collectState==1}" ref="list" @click="shoucang(index,it.id)"></i></div>
            <div class="info">
              <span><img src="../../assets/images/museum/address.png" alt=""><i>{{it.detailAddress}}</i></span>
              <span><img src="../../assets/images/museum/juli.png" alt=""><i>{{it.meter}}</i></span>
            </div>
            <router-link :to="{path:'/museumDetail',query:{id:it.id}}"><img class="muimg" :src="it.imageUrl" alt=""></router-link>
            <div class="display" v-show="it.headLine"><span>{{it.exhibitionTypeDes}}</span><span>{{it.headLine}}</span></div>
          </div>
          <div class="more" @click="more()" v-if="data.length != 0">查看更多</div>
        </div>
        <div v-if="data.length === 0">
          <div class="g-no-data" style="margin-top:0;">
            <div class="no-data-bg"></div>
            <p class="no-data-txt">暂无数据</p>
          </div>
        </div>
      </div>
      <pop :po="popp"></pop>
    </div>
</template>

<script>
  import pop from '../../components/popup';
  import { Indicator, Toast } from 'mint-ui';
    export default {
        name: "museumList",
      data(){
          return{
            data:[],
            pageNum:1,
            totalNum:0,
            key:"",
            popp:false,
            quyuId:"",
            museumId:"",
            latitude:"",
            longitude:"",
            orderType:1,
          }
      },
      components:{
        pop
      },
      methods:{
        ss(){
          this.key=this.$refs.lk.value;
          this.getData();
        },
        ch(){
          this.popp=false;
        },
        we(){
          if(this.popp){
            this.popp=false;
          }else{
            this.popp=true;
          }
        },
        getData(quyuId,museumId){
          this.popp=false;
          this.pageNum=1;
          this.quyuId=quyuId;
          this.museumId=museumId;
          if(museumId){
            this.$router.push({ path: '/museumDetail', query: { id: museumId }});
            return false;
          }
          this.$http.get("/m/Guide/getMuseumSearchList.do",{hideLoadig: true,currentPage:this.pageNum,area:this.quyuId,latitude:this.latitude,longitude:this.longitude,name:this.key,orderType:this.orderType,userId: this.$store.getters.userid, token: this.$store.getters.token}).then((res)=>{
            ;
            this.data=res.data.data;
            this.totalNum=res.data.page.totalPage;
          })
        },
        choose(num){
          this.pageNum=1;
          if(num==0){
            this.$refs.type1.classList.remove("active");
            this.$refs.type2.classList.remove("active");
            this.$refs.type0.classList.add("active");
            this.orderType=1;
            this.getData();
          }else if(num==1){
            this.$refs.type0.classList.remove("active");
            this.$refs.type2.classList.remove("active");
            this.$refs.type1.classList.add("active");
            this.orderType=2;
            this.getData();
          }else if(num==2){
            this.$refs.type1.classList.remove("active");
            this.$refs.type0.classList.remove("active");
            this.$refs.type2.classList.add("active");
            this.orderType=3;
            this.getData();
          }
        },
        more(){
          if(this.pageNum==this.totalNum){
            Toast('已全部加载');
            return false;
          }else{
            this.pageNum++;
            this.$http.get("/m/Guide/getMuseumSearchList.do",{hideLoadig: true,currentPage:this.pageNum,area:this.quyuId,latitude:this.latitude,longitude:this.longitude,name:this.key,orderType:this.orderType,userId: this.$store.getters.userid, token: this.$store.getters.token}).then((res)=>{
              ;
              this.data=this.data.concat(res.data.data);
            })
          }
        },
        shoucang(index,id){
          if(!this.$store.getters.userid) {
            this.$router.push({ path: '/my/login'});
            return;
          }
          if(this.$refs.list[index].classList.contains("active")==false){
            this.$http.get("/m/myInfo/addCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:2}).then((res)=>{
              ;
              if(res.data.success==1){
                this.$refs.list[index].classList.add("active");
                Toast('收藏成功');
              } else if(res.data.success == 999) {
                this.$common.confirmLogin();
              } else{
                Toast('收藏失败');
              }
            })
          }else{
            this.$http.get("/m/myInfo/delCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:2}).then((res)=>{
              ;
              if(res.data.success==1){
                this.$refs.list[index].classList.remove("active");
                Toast('取消收藏成功');
              } else if(res.data.success == 999) {
                this.$common.confirmLogin();
              } else{
                Toast('取消收藏失败');
              }
            })
          }
        },
        // 获取经纬度
        getLatitude() {
          let vm = this;
          if(geolocation){
            geolocation=null
          }else{
            var geolocation = new BMap.Geolocation();
            Indicator.open({spinnerType: 'fading-circle'});
            geolocation.getCurrentPosition(function(r){
              Indicator.close();
              if(this.getStatus() == BMAP_STATUS_SUCCESS){
                vm.latitude = r.point.lat;
                vm.longitude = r.point.lng;
                vm.getData();
              }
              else {
                console.log('failed'+this.getStatus());
              }
            }, function() {
              Indicator.close();
            }, {enableHighAccuracy: true});
          }

        }
      },
      mounted(){
          this.getLatitude();
      }
    }
</script>

<style lang="less" scoped>
  .main{
    width: 100%;
    .title{
      margin-top: 1px;
      height: 3.5rem;
      padding-top: 1rem;
      padding-left: 1.25rem;
      background: #fff;
      .list{
        height: 1.5rem;
      }
      input{
        width:7.85rem;
        height:1.5rem;
        border-radius:0.75rem;
        vertical-align: top;
        margin-left: 0.8rem;
        font-size:0.6rem;
        font-weight:400;
        color:rgba(185,185,185,1);
        line-height:1.5rem;
        padding-left: 1.8rem;
        background:rgb(241,241,241) url("../../assets/images/museum/sousuo.png") no-repeat 0.45rem 0.3rem;
      }
      span{
        display: inline-block;
        margin-left: 1rem;
        font-size:0.65rem;
        color:rgba(185,185,185,1);
        font-weight:400;
        vertical-align: top;
        height: 1.5rem;
        line-height: 1.5rem;
        i{
          vertical-align: top;
          &.active{
            color:rgba(72,165,255,1);
          }
        }
      }
      div{
        position: absolute;
        width: 1.3rem;
        height: 1.3rem;
        top: 1.25rem;
        left:4rem;
        z-index: 100;
      }
    }
    .line{
      background:rgba(241,241,241,1);
      height: 0.5rem;
    }
    .cont{
      width: 100%;
      background-color: #fff;
      position: absolute;
      top: 4rem;
      bottom: 0;
      overflow-y: scroll;
      &.cur{
        top: 6rem;
      }
      .cont_wrap{
        width: 100%;
        padding: 1rem 1.25rem 0;
        .item{
          margin-top: 2rem;
          &:nth-child(1){
            margin-top: 0;
          }
          .ti{
            font-size:0.75rem;
            font-weight:bold;
            color:rgba(102,102,102,1);
            line-height:1.05rem;
            i{
              display: inline-block;
              float: right;
              margin-right: 0.2rem;
              margin-top: 0.2rem;
              width: 0.7rem;
              height: 0.7rem;
              background: url("../../assets/images/museum/xing1.png");
              background-size: 100% 100%;
              &.active{
                background: url("../../assets/images/museum/xing2.png");
                background-size: 100% 100%;
              }
            }
          }
          .info{
            margin-top: 0.3rem;
            span{
              font-size:0.65rem;
              font-weight:400;
              color:rgba(185,185,185,1);
              display: inline-block;
              height: 0.8rem;
              line-height: 0.8rem;
              i{
                vertical-align: top;
                margin-left: 0.5rem;
              }
              &:nth-child(2){
                display: inline-block;
                float: right;
                margin-right: 0.2rem;
              }
            }
          }
          .muimg{
            width:100%;
            height:12.2rem;
            border-radius:0.5rem;
            margin-top: 0.4rem;
          }
          .display{
            margin-top: 0.55rem;
            span{
              &:nth-child(1){
                display: inline-block;
                text-align: center;
                width:3.55rem;
                height:1.05rem;
                background:rgba(72,165,255,1);
                border-radius:0.6rem;
                border:0.05rem solid;
                font-size:0.65rem;
                font-weight:400;
                color:rgba(255,255,255,1);
                line-height:1.05rem;
              }
              &:nth-child(2){
                font-size:0.75rem;
                font-weight:bold;
                color:rgba(102,102,102,1);
                line-height:1.05rem;
                margin-left: 0.5rem;
              }
            }
          }
        }
        .more{
          width:16.25rem;
          height:1.5rem;
          background:rgba(243,245,250,1);
          border-radius:0.25rem;
          font-size:0.65rem;
          text-align: center;
          font-weight:bold;
          color:rgba(91,102,160,1);
          line-height:1.5rem;
          margin:1.3rem 0 1.3rem;
          touch-action: none;
        }
      }
    }
  }

</style>
<style>
  html,body,.main{
    width:100%;
    height: 100%;
  }
</style>



