<template>
  <div class="main">
    <div class="title">
      <span>{{data.level}}</span>
      <span>{{info.name}}</span>
      <span><img src="../../assets/images/museum/eye.png" alt=""><span>{{data.count}}</span></span>
    </div>
    <img class="muimg" :src="info.imageUrl" alt="">
    <div class="shoucang" v-show="!iscollected" @click="chou(1,info.id)">收藏</div>
    <div class="shoucang active" v-show="iscollected" @click="chou(2,info.id)">已收藏</div>
    <div class="tab">
      <div><span v-for="(ta,index) in tabList" @click="toggle(index)" :class="{active:tabNum==index}">{{ta}}</span></div>
    </div>
    <div class="info">
      <div class="infoList">
        <div class="info1" v-show="tabNum==0">
          <div class="t1"><i></i><span>参观信息</span></div>
          <div class="t2"><img src="../../assets/images/museum/tiem.png" alt=""><span>{{info.staticCloseDay}}</span></div>
          <div class="t2"><img src="../../assets/images/museum/dili.png" alt=""><span>{{info.detailAddress}}</span></div>
          <div class="t3"><i></i><span>博物馆简介</span></div>
          <div class="intr">{{info.description}}</div>
        </div>
        <div class="info2" v-show="tabNum==1">
          <div class="display_item" v-for="di in data.display">
            <router-link :to="{path:'/displayDetail',query:{id:di.id}}">
            <img :src="di.pictureId" alt="">
            </router-link>
            <!--<div class="type">{{di.type}}</div>-->
            <div>
              <p>{{di.headline}}</p>
              <p>{{di.openAddress}}</p>
              <p>{{di.openTime}}</p>
            </div>
          </div>
          <div class="more" @click="more()">查看更多</div>
        </div>
        <div class="info3" v-show="tabNum==2">
          <div class="cp_item" v-for="co in data.collect">
            <img class="cp_img" :src="co.pics[0].url" alt="">
            <span>{{co.name}}</span>
            <router-link :to="{path:'/collectDetail',query:{id:co.id}}"><img class="next" src="../../assets/images/museum/next.png" alt=""></router-link>
          </div>
          <div class="more" @click="more1()">查看更多</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { Toast } from 'mint-ui';
    export default {
        name: "museumDetail",
      data(){
          return{
            tabList:["简介","展览","馆藏"],
            tabNum:0,
            pageNum:1,
            totalNum:1,
            pageNum1:1,
            totalNum1:5,
            iscollected:0,
            info:"",
            data:{
              level:"一级",
              name:"天津博物馆",
              count:365,
              img:"http://www.chnmuseum.cn/Portals/0/PictureNewsSettings/591/20161208-3636168000349044000.JPG",
              time:"每周二至周四 9:00-17:00（16:00停止入馆）",
              address:"首都博物馆西区一层B展厅",
              cont:"展厅面积840平米，共展出馆藏古代青铜器、玉器、书画、瓷器、工艺等类别文物中最具代表性的艺术珍品63件套，通过对每一件珍品文物的深度诠释，凸显中华文明的悠久醇厚，更让观众从中真实感受古代中国人高度的艺术想象力和创造力以及天津深厚的文物集藏传统。 Selected Rare Relics Collected By Tianjin Museum （耀世奇珍——馆藏文物精品陈列）",
              display:[],
              collect:[]
            }
          }
      },
      methods:{
        toggle(index){

          this.tabNum=index;
        },
        more(){
          if(this.pageNum==this.totalNum){
            Toast('已全部加载');
            return false;
          }else{
            this.pageNum++;
            this.$http.get("/m/spreadtrum/getSpreadtrumList.do").then((res)=>{
              ;
              this.data.display=this.data.display.concat(res.data.data.showList);
            })
          }
        },
        more1(){
          if(this.pageNum1==this.totalNum1){
            Toast('已全部加载');
            return false;
          }else{
            this.pageNum1++;
            this.$http.get("/m/collection/getCollectionList.do").then((res)=>{
              ;
              this.data.collect=this.data.collect.concat(res.data.data);
            })
          }
        },
        chou(num,id){
          if(!this.$store.getters.userid) {
            this.$router.push({ path: '/my/login'});
            return;
          }
          if(num==1){
            this.$http.get("/m/myInfo/addCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:2}).then((res)=>{
              ;
              if(res.data.success==1){
                Toast('收藏成功');
                this.iscollected=1;
              } else if(res.data.success == 999) {
                this.$common.confirmLogin();
              } else{
                Toast('收藏失败');
              }
            })
          }else{
            this.$http.get("/m/myInfo/delCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:2}).then((res)=>{
              ;
              if(res.data.success==1){
                Toast('取消收藏成功');
                this.iscollected=0;
              } else if(res.data.success == 999) {
                this.$common.confirmLogin();
              } else{
                Toast('取消收藏失败');
              }
            })
          }
        }
      },
      mounted(){
        this.id=this.$route.query.id;
        this.$http.get("/m/Guide/getMuseumDetail.do",{museumId:this.id,userid: this.$store.getters.userid, token: this.$store.getters.token}).then((res)=>{
          ;
          this.info=res.data.data;
          this.iscollected=this.info.iscollection;
        })
        this.$http.get("/m/spreadtrum/getSpreadtrumList.do",{museumId:this.id,currentPage:this.pageNum}).then((res)=>{
          ;
          this.data.display=res.data.data.showList;
          this.totalNum=res.data.page.totalPage;
        })
        this.$http.get("/m/collection/getCollectionList.do",{museumId:this.id,currentPage:this.pageNum1}).then((res)=>{
          ;
          this.data.collect=res.data.data;
          this.totalNum1=res.data.page.totalPage;
        })

      }
    }
</script>

<style lang="less" scoped>
  .main{
    width: 100%;
    height: 100%;
    background:rgba(241,241,241,1);
    position: relative;
    .title{
      height: 3rem;
      margin-top: 2px;
      padding-top: 1rem;
      background-color: #fff;
      span{
        display: inline-block;
        vertical-align: top;
        &:nth-child(1){
          width:2.3rem;
          height:1.05rem;
          background:rgba(72,165,255,1);
          border-radius:0.6rem;
          border:0.05rem solid;
          font-size:0.65rem;
          font-weight:400;
          color:rgba(255,255,255,1);
          line-height:1rem;
          text-align: center;
          margin-left: 1.25rem;
        }
        &:nth-child(2){
          font-size:0.75rem;
          font-weight:bold;
          color:rgba(102,102,102,1);
          height:1.05rem;
          line-height: 1rem;
          margin-left: 0.5rem;
        }
        &:nth-child(3){
          display: inline-block;
          height:1.05rem;
          margin-right: 1.3rem;
          float: right;
          img{

          }
          span{
            display: inline-block;
            font-size:0.65rem;
            font-weight:400;
            color:rgba(185,185,185,1);

            width:1.15rem;
            height:1.05rem;
            line-height: 1.2rem;
          }
        }
      }
    }
    .muimg{
      width: 100%;
      height: 14.05rem;
    }
    .shoucang{
      width:2.4rem;
      height:1.5rem;
      background:rgba(243,245,250,1);
      border-radius:0.25rem;
      position: absolute;
      top: 4.3rem;
      right: 1.25rem;
      font-size:0.7rem;
      font-weight:bold;
      color:rgba(91,102,160,1);
      line-height:1.5rem;
      text-align: center;
      &.active{
        background:rgba(72,165,255,1);
        color: #fff;
      }
    }
    .tab {
      position: absolute;
      top: 16.25rem;
      height: 1.75rem;
      width: 100%;
      padding-top: 0.75rem;
      font-size: 0.65rem;
      font-weight: 400;
      color: rgba(102, 102, 102, 1);
      line-height: 0.9rem;
      border-radius: 0.75rem 0.75rem 0 0;
      background-color: #fff;
      z-index: 1000;
      div {
        width: 14.5rem;
        margin: 0 auto;
        height: 1.75rem;
        span {
          text-align: center;
          display: inline-block;
          margin-left: 5.1rem;
          height: 1rem;
          touch-action: none;
          &:nth-child(1) {
            margin-left: 0;
          }
          &.active {
            color: rgba(72, 165, 255, 1);
            border-bottom: 2px solid rgba(72, 165, 255, 1);;
          }
        }
      }
    }
    .info{
      width: 100%;
      position: absolute;
      top: 17rem;
      bottom: 0;
      overflow-y: scroll;
      background-color: #fff;
      padding-top: 0.75rem;
      .infoList{
        margin-top: 0.95rem;
        .info1{
          .t1{
            i{
              display: inline-block;
              width:0.19rem;
              height:0.65rem;
              background:rgba(72,165,255,1);
            }
            span{
              font-size:0.7rem;
              font-weight:400;
              color:rgba(40,40,40,1);
              margin-left: 1rem;
            }
          }
          .t2{
            margin-left: 1.35rem;
            img{
              vertical-align: top;
            }
            span{
              font-size:0.7rem;
              font-weight:400;
              color:rgba(102,102,102,1);
              vertical-align: top;
              margin-left: 0.6rem;
              display: inline-block;
              width: 15rem;
            }
            &:nth-child(2){
              margin-top: 0.9rem;
            }
            &:nth-child(3){
              margin-top: 0.35rem;
            }
          }
          .t3{
            margin-top: 1rem;
            i{
              display: inline-block;
              width:0.19rem;
              height:0.65rem;
              background:rgba(72,165,255,1);
            }
            span{
              font-size:0.7rem;
              font-weight:400;
              color:rgba(40,40,40,1);
              margin-left: 1rem;
            }
          }
          .intr{
            margin:1rem auto;
            width:16.25rem;
            height:8rem;
            font-size:0.7rem;
            font-weight:400;
            color:rgba(102,102,102,1);
            line-height:1rem;
            text-align: justify;
          }
        }
        .info2{
          .display_item{
            width: 16.4rem;
            margin: 1.2rem auto 0;
            position: relative;
            img{
              width: 6rem;
              height: 4.5rem;
              border-radius: 4px;
            }
            /*.type{
              width:3.26rem;
              height:1.05rem;
              background:rgba(72,165,255,1);
              border-radius:0rem 0.25rem 0rem 0rem;
              position: absolute;
              left: 0;
              bottom: 0;
              font-size:0.65rem;
              font-weight:400;
              color:rgba(255,255,255,1);
              margin-left: 0;
            }*/
            div{
              display: inline-block;
              width: 8.75rem;
              vertical-align: top;
              margin-left: 1.2rem;
              p{
                &:nth-child(1){
                  width:8.75rem;
                  font-size:0.75rem;
                  font-weight:bold;
                  color:rgba(102,102,102,1);
                  line-height:1.05rem;
                }
                &:nth-child(2){
                  font-size:0.65rem;
                  font-weight:400;
                  color:rgba(102,102,102,1);
                  margin-top: 0.75rem;
                }
                &:nth-child(3){
                  font-size:0.65rem;
                  font-weight:400;
                  color:rgba(185,185,185,1);
                  line-height:0.9rem;
                  margin-top: 0.1rem;
                }
              }
            }
          }
          .more{
            width:16.25rem;
            height:1.5rem;
            background:rgba(243,245,250,1);
            border-radius:0.25rem;
            font-size:0.65rem;
            text-align: center;
            font-weight:bold;
            color:rgba(91,102,160,1);
            line-height:1.5rem;
            touch-action: none;
            margin: 1.3rem auto 1.3rem;
          }
        }
        .info3{
          .cp_item{
            width: 16.5rem;
            margin: 0 auto;
            height: 3rem;
            border-bottom:1px solid #f3f3f3;
            line-height: 3rem;
            .cp_img{
              width: 2rem;
              height: 2rem;
              border-radius: 4px;
              margin-top: 0.5rem;
            }
            span{
              vertical-align: top;
              font-size:0.75rem;
              font-weight:bold;
              color:rgba(102,102,102,1);
              display: inline-block;
              height: 3rem;
              line-height: 3rem;
              margin-left: 0.8rem;
              width: 10rem;
              overflow:hidden; //超出的文本隐藏
              text-overflow:ellipsis; //溢出用省略号显示
              white-space:nowrap; //溢出不换行
            }
            .next{
              vertical-align: top;
              width: 0.7rem;
              height: 0.7rem;
              margin-top: 1.15rem;
              margin-right: 1.4rem;
              float: right;
            }
          }
          .more{
            width:16.25rem;
            height:1.5rem;
            background:rgba(243,245,250,1);
            border-radius:0.25rem;
            font-size:0.65rem;
            text-align: center;
            font-weight:bold;
            color:rgba(91,102,160,1);
            line-height:1.5rem;
            margin:1.3rem auto 1.3rem;
            touch-action: none;
          }
        }
      }
      }

    }
</style>



