<template>
  <div class="main">
    <div class="title">
      <img class="list" src="../../assets/images/museum/list.png" alt="" @click="we()">
      <input ref="lk" type="text" placeholder="输入博物馆名称" maxlength="10" v-model="key" @keyup.enter="ss">
      <span><i :class="{active:tab}" ref="type0" @click="choose(2)">讲座</i> | <i :class="{active:!tab}" ref="type1" @click="choose(1)">活动</i></span>
      <div @click="ss()"></div>
    </div>
    <div class="cont">
      <div class="mo" v-for="(li,index1) in list" ref="list1">
        <div class="line"></div>
        <div class="co">
          <div class="t1">{{li.month}}</div>
          <div class="item" v-for="(it,index) in li.list">
            <div><img src="../../assets/images/display/icon.png" alt=""><span>{{it.name}}</span><i :class="{active:it.isCollection==1}" ref="list2" @click="shoucang(index,index1,it.activityId)"></i></div>
            <div>{{it.activityName}}</div>
            <div>
              <router-link :to="{path:'/activityDetail',query:{id:it.activityId,type:type}}">
              <div class="t2"><img src="../../assets/images/museum/tiem.png" alt=""><span>{{it.activityTime}}</span></div>
              <div class="t2"><img src="../../assets/images/museum/dili.png" alt=""><span>{{it.activityPlace}}</span><img src="../../assets/images/museum/next.png" alt=""></div>
              </router-link>
            </div>
          </div>
        </div>
      </div>
      <div v-if="list.length === 0">
        <div class="g-no-data" style="margin-top:0;">
          <div class="no-data-bg"></div>
          <p class="no-data-txt">暂无数据</p>
        </div>
      </div>
    </div>
    <pop :po="popp"></pop>
  </div>
</template>

<script>
  import pop from '../../components/popup';
  import { Toast } from 'mint-ui';
    export default {
        name: "index",
      data(){
          return{
            list:[],
            tab:true,
            key:"",
            popp:false,
            quyuId:"",
            museumId:"",
            type:"2"
          }
      },
      components:{
        pop
      },
      methods:{
        ss(){
          this.key=this.$refs.lk.value;
          this.getData();
        },
        ch(){
          this.popp=false;
        },
        we(){
          if(this.popp){
            this.popp=false;
          }else{
            this.popp=true;
          }
        },
        getData(quyuId,museumId){
          this.popp=false;
          this.quyuId=quyuId;
          this.museumId=museumId;
          this.$http.get("/m/activity/getActivityListByType.do",{area:this.quyuId,museumId:this.museumId,key:this.key,activityCategory:this.type, userId: this.$store.getters.userid, token: this.$store.getters.token}).then((res)=>{
            ;
            this.list=res.data.data;
          })
        },
        choose(num){
          if(num==2){
            this.tab=true;
            this.type="2";
            this.museumId=''
          }else{
            this.tab=false;
            this.type="1";
            this.museumId=''
          }
          this.getData();
        },
        shoucang(index,index1,id){
          if(!this.$store.getters.userid) {
            this.$router.push({ path: '/my/login'});
            return;
          }
          if(this.$refs.list1[index1].querySelectorAll("i")[index].classList.contains("active")==false){
            this.$http.get("/m/myInfo/addCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:4}).then((res)=>{
              ;
              if(res.data.success==1){
                this.$refs.list1[index1].querySelectorAll("i")[index].classList.add("active");
                Toast('收藏成功');
              } else if(res.data.success == 999) {
                this.$common.confirmLogin();
              } else{
                Toast('收藏失败');
              }
            })
          }else{
            this.$http.get("/m/myInfo/delCollect.do",{collectID:id,userID: this.$store.getters.userid, token: this.$store.getters.token,type:4}).then((res)=>{
              ;
              if(res.data.success==1){
                this.$refs.list1[index1].querySelectorAll("i")[index].classList.remove("active");
                Toast('取消收藏成功');
              } else if(res.data.success == 999) {
                this.$common.confirmLogin();
              } else{
                Toast('取消收藏失败');
              }
            })
          }
          return false;
        },
      },
      mounted(){
          this.getData();
        /*this.$http.get("/m/activity/getActivityListByType.do",{activityCategory:this.type}).then((res)=>{
          ;
          this.list=res.data.data;
        })*/
      }
    }
</script>

<style lang="less" scoped>
  .main{
    width: 100%;
    .title{
      margin-top: 1px;
      height: 3.5rem;
      padding-top: 1rem;
      padding-left: 1.25rem;
      background: #fff;
      .list{
        height: 1.5rem;
      }
      input{
        width:9.35rem;
        height:1.5rem;
        border-radius:0.75rem;
        vertical-align: top;
        margin-left: 0.8rem;
        font-size:0.6rem;
        font-weight:400;
        color:rgba(185,185,185,1);
        line-height:1.5rem;
        padding-left: 1.8rem;
        background:rgb(241,241,241) url("../../assets/images/museum/sousuo.png") no-repeat 0.45rem 0.3rem;
      }
      span {
        display: inline-block;
        margin-left: 0.5rem;
        font-size: 0.65rem;
        color: rgba(185, 185, 185, 1);
        font-weight: 400;
        vertical-align: top;
        height: 1.5rem;
        line-height: 1.5rem;
        i {
          vertical-align: top;
          &.active {
            color: rgba(72, 165, 255, 1);
          }
        }
      }
      div{
        position: absolute;
        width: 1.3rem;
        height: 1.3rem;
        top: 1.25rem;
        left:4rem;
        z-index: 100;
      }
    }
    .cont{
      width: 100%;
      position: absolute;
      top: 3.5rem;
      bottom: 0;
      overflow-y: scroll;
      .mo{
        .line{
          width: 100%;
          height: 0.5rem;
          background:rgba(241,241,241,1);
        }
        .co{
          padding: 0 1.25rem;
          margin-top: 1rem;
          margin-bottom: 0.5rem;
          .t1{
            font-size:0.85rem;
            font-weight:bold;
            color:rgba(40,40,40,1);
            line-height:1.2rem;
          }
          .item{
            margin-top: 1rem;
            padding-bottom: 0.85rem;
            border-bottom:0.05rem solid rgba(241,241,241,1);
            &:last-child{
              border-bottom: none;
            }
            div{
              &:nth-child(1){
                height: 0.95rem;
                img{
                  width:0.95rem;
                  height:0.95rem;
                }
                span{
                  display: inline-block;
                  font-size:0.65rem;
                  font-family:PingFang-SC-Regular;
                  font-weight:400;
                  color:rgba(102,102,102,1);
                  height: 0.95rem;
                  line-height: 0.95rem;
                  vertical-align: top;
                  margin-left: 0.25rem;
                }
                i{
                  display: inline-block;
                  float: right;
                  margin-right: 0.2rem;
                  margin-top: 0.2rem;
                  width: 0.7rem;
                  height: 0.7rem;
                  background: url("../../assets/images/museum/xing1.png");
                  background-size: 100% 100%;
                  &.active{
                    background: url("../../assets/images/museum/xing2.png");
                    background-size: 100% 100%;
                  }
                }
              }
              &:nth-child(2){
                margin-top: 0.5rem;
                font-size:0.75rem;
                font-weight:bold;
                color:rgba(102,102,102,1);
                line-height:1.05rem;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                /*! autoprefixer: off */
                -webkit-box-orient: vertical;
                /* autoprefixer: on */
              }
              &:nth-child(3){
                .t2{
                  margin-top: 0.5rem;
                  img{
                    vertical-align: top;
                    width: 0.62rem;
                    height: 0.75rem;
                  }
                  span{
                    font-size:0.7rem;
                    font-weight:400;
                    color:rgba(102,102,102,1);
                    vertical-align: top;
                    margin-left: 0.6rem;
                    height: 0.75rem;
                    line-height: 0.75rem;
                    display: inline-block;
                  }
                  &:nth-child(2){
                    margin-top: 0.35rem;
                    img{
                      &:nth-child(3){
                        width: 0.7rem;
                        height: 0.7rem;
                        float: right;
                        margin-right: 0.2rem;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
</style>



