<template>
  <div class="main">
    <div class="title">
      <img class="list" src="../../assets/images/museum/list.png" alt="" @click="we()">
      <input ref="lk" type="text" placeholder="输入博物馆名称" maxlength="10">
      <span><i :class="{active:tab}" ref="type0" @click="choose(0)">新闻</i> | <i :class="{active:!tab}" ref="type1" @click="choose(1)">公告</i></span>
      <div @click="ss()"></div>
    </div>
    <div class="cont">
      <div class="mo" v-for="(li,index1) in list" ref="list1">
        <div class="line"></div>
        <div class="co" v-show="tab">
          <div class="t1">{{li.month}}</div>
          <div class="item" v-for="(it,index) in li.list">
            <router-link :to="{path:'/informationDetail',query:{id:it.id,type:0}}">
            <div>
              <p>{{it.headline}}<img class="fire" src="../../assets/images/information/fire.png" alt=""></p>
              <p>{{it.description}}</p>
              <p>
                <!-- <img src="../../assets/images/display/icon.png" alt=""> -->
                <img :src="it.museumUrl" alt="" v-if="it.museumUrl">
                <span>{{it.publisher}}</span><i>{{it.createTime}}</i>
              </p>
            </div>
            <img class="um" :src="it.picUrl" alt="">
            </router-link>
          </div>
        </div>
        <div class="co" v-show="!tab">
          <div class="t1">{{li.month}}</div>
          <div class="item" v-for="(it,index) in li.list">
            <router-link :to="{path:'/informationDetail',query:{id:it.id,type:1}}">
            <div>
              <p>{{it.headline}}<img class="fire" src="../../assets/images/information/fire.png" alt=""></p>
              <p>{{it.description}}</p>
              <p><img src="../../assets/images/display/icon.png" alt=""><span>{{it.publisher}}</span><i>{{it.createTime}}</i></p>
            </div>
            </router-link>
          </div>
        </div>
      </div>
    </div>
    <pop :po="popp"></pop>
  </div>
</template>

<script>
  import pop from '../../components/popup';
  export default {
    name: "index",
    data(){
      return{
        list:[],
        tab:true,
        type:"xwdt",
        key:"",
        popp:false,
        quyuId:"",
        museumId:""
      }
    },
    components:{
      pop
    },
    methods:{
      ss(){
        this.key=this.$refs.lk.value;
        this.$http.get("/m/article/getArticleListByUniqueType.do",{area:this.quyuId,museumId:this.museumId,key:this.key,uniqueName:this.type}).then((res)=>{
          ;
          this.list=res.data.data;
        })
      },
      ch(){
        this.popp=false;
      },
      we(){
        if(this.popp){
          this.popp=false;
        }else{
          this.popp=true;
        }
      },
      getData(quyuId,museumId){
        this.popp=false;
        this.pageNum=1;

        this.quyuId=quyuId;
        this.museumId=museumId;
        this.$http.get("/m/article/getArticleListByUniqueType.do",{area:this.quyuId,museumId:this.museumId,key:this.key,uniqueName:this.type}).then((res)=>{
          ;
          this.list=res.data.data;
        })
      },
      choose(num){
        if(num==0){
          this.tab=true;
          this.type="xwdt";
          this.museumId=""
        }else{
          this.tab=false;
          this.museumId=""
          this.type="tzgg";
        }
        this.$http.get("/m/article/getArticleListByUniqueType.do",{area:this.quyuId,museumId:this.museumId,key:this.key,uniqueName:this.type}).then((res)=>{
          ;
          this.list=res.data.data;
        })
      },
    },
    mounted(){
      this.$http.get("/m/article/getArticleListByUniqueType.do",{uniqueName:this.type}).then((res)=>{
        ;
        this.list=res.data.data;
      })
    }
  }
</script>

<style lang="less" scoped>
  .main{
    width: 100%;
    .title{
      margin-top: 1px;
      height: 3.5rem;
      padding-top: 1rem;
      padding-left: 1.25rem;
      background: #fff;
      position: relative;
      .list{
        height: 1.5rem;
      }
      input {
        width: 9.35rem;
        height: 1.5rem;
        border-radius: 0.75rem;
        vertical-align: top;
        margin-left: 0.8rem;
        font-size: 0.6rem;
        font-weight: 400;
        color: rgba(185, 185, 185, 1);
        line-height: 1.5rem;
        padding-left: 1.8rem;
        background: rgb(241, 241, 241) url("../../assets/images/museum/sousuo.png") no-repeat 0.45rem 0.3rem;
      }
      span {
        display: inline-block;
        margin-left: 0.5rem;
        font-size: 0.65rem;
        color: rgba(185, 185, 185, 1);
        font-weight: 400;
        vertical-align: top;
        height: 1.5rem;
        line-height: 1.5rem;
        i {
          vertical-align: top;
          &.active {
            color: rgba(72, 165, 255, 1);
          }
        }
      }
      div{
        position: absolute;
        width: 1.3rem;
        height: 1.3rem;
        top: 1.25rem;
        left:4rem;
        z-index: 100;
      }
    }
    .cont{
      width: 100%;
      position: absolute;
      top: 3.5rem;
      bottom: 0;
      overflow-y: scroll;
      .mo{
        .line{
          width: 100%;
          height: 0.5rem;
          background:rgba(241,241,241,1);
        }
        .co{
          padding: 0 1.25rem;
          margin-top: 1rem;
          margin-bottom: 0.5rem;
          .t1{
            font-size:0.85rem;
            font-weight:bold;
            color:rgba(40,40,40,1);
            line-height:1.2rem;
          }
          .item{
            margin-top: 1rem;
            padding-bottom: 0.85rem;
            border-bottom:0.05rem solid rgba(241,241,241,1);
            &:last-child{
              border-bottom: none;
            }
            div{
              height: 6rem;
              width: 8.75rem;
              display: inline-block;
              p{
                &:nth-child(1){
                  font-size:0.75rem;
                  font-family:PingFang-SC-Bold;
                  font-weight:bold;
                  color:rgba(102,102,102,1);
                  line-height:1.05rem;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  display: -webkit-box;
                  -webkit-line-clamp: 2;
                  /*! autoprefixer: off */
                  -webkit-box-orient: vertical;
                  /* autoprefixer: on */
                  .fire{
                    width:0.4rem;
                    height:0.5rem;
                    margin-left: 0.2rem;
                    transform: translateY(-0.1rem);
                  }
                }
                &:nth-child(2){
                  font-size:0.65rem;
                  font-family:PingFang-SC-Regular;
                  font-weight:400;
                  color:rgba(185,185,185,1);
                  line-height:0.9rem;
                  margin-top: 0.6rem;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  display: -webkit-box;
                  -webkit-line-clamp: 2;
                  /*! autoprefixer: off */
                  -webkit-box-orient: vertical;
                  /* autoprefixer: on */
                }
                &:nth-child(3){
                  height: 0.95rem;
                  margin-top: 0.5rem;
                  img{
                    width:0.95rem;
                    height:0.95rem;
                    border-radius: 100%;
                  }
                  span{
                    display: inline-block;
                    font-size:0.65rem;
                    font-weight:400;
                    color:rgba(102,102,102,1);
                    height: 0.95rem;
                    line-height: 0.95rem;
                    vertical-align: top;
                    margin-left: 0.25rem;
                  }
                  i{
                    display: inline-block;
                    vertical-align: top;
                    font-size:0.65rem;
                    height: 0.95rem;
                    line-height: 0.95rem;
                    font-weight:400;
                    color:rgba(185,185,185,1);
                    float: right;
                    margin-right: -7.5rem;
                  }
                }
              }
            }
            .um{
              width: 6rem;
              height: 4.5rem;
              border-radius: 5px;
              vertical-align: top;
              float: right;
            }
          }
          &:nth-child(3){
            .item{
              div{
                width: 100%;
                p{
                  &:nth-child(3){
                    i{
                      margin-right: 0;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
</style>



