<template>
  <div class="main">
    <div class="title">
      <img class="list" src="../../assets/images/museum/list.png" alt=""  @click="we()">
      <input type="text" ref="lk" placeholder="输入博物馆名称/专题名称" v-model="key" maxlength="10">
      <span><i ref="type1" :class="{active:tab}" @click="choose(0)">距离</i> | <i ref="type2" :class="{active:!tab}" @click="choose(1)">火热</i></span>
      <div @click="ss()"></div>
    </div>
    <div class="line"></div>
    <div class="cont">
      <div class="cont_wrap">
        <div class="item" v-for="(it,index) in list" @click="gotoDetail(it.id)">
          <div class="ti">

            <img class="g-icon icon-info" ref="list" alt=""
              :src="it.museumPicture"
              v-if="it.museumPicture"
              @click="shoucang(index)">{{it.museumName}}

            <!-- <i ref="list" @click="shoucang(index)"></i>{{it.museumName}}  -->
          </div>
          <!--<div class="info">
            <span><img src="../../assets/images/museum/address.png" alt=""><i>{{it.address}}</i></span>
            <span><img src="../../assets/images/museum/juli.png" alt=""><i>{{it.juli}}</i></span>
          </div>-->
          <img class="muimg" :src="it.iconUrl" alt="" :onerror="defaultImg">
          <div class="display"><span>{{it.name}}</span></div>
          <div class="distype">
            <span v-if="it.label&&it.label.split(',').length>0">{{it.label?it.label.split(",")[0]:''}}</span>
            <span v-if="it.label&&it.label.split(',').length>1">{{it.label?it.label.split(",")[1]:''}}</span>
          </div>
          <div class="disbottom">
            <p>{{it.collectionNum}}</p>
             <div class="flext">
            <div>
                <i></i><span>{{it.meterStr}}</span>
                 <i></i><span>{{it.listenNumber}}</span>
            </div>
               <p>藏品</p>
             </div>
          </div>
        </div>
        <div class="more" @click="more()" v-if="list.length != 0">查看更多</div>
      </div>
      <div v-if="list.length === 0">
        <div class="g-no-data" style="margin-top:0;">
          <div class="no-data-bg"></div>
          <p class="no-data-txt">暂无数据</p>
        </div>
      </div>
    </div>
    <pop :po="popp"></pop>
    <cmp-footer :navType="2"></cmp-footer>
  </div>
</template>

<script>
  import cmpFooter from '@cmp/footer'
  import { Indicator } from 'mint-ui'
  import { Toast } from 'mint-ui';
  import pop from '../../components/popup';
  export default {
    name: "museumList",
    components: {
      cmpFooter,
      pop
    },
    data(){
      return{
        data:[],
        defaultImg: this.$store.getters.defaultImg,
        linshi:[],
        tab:true,
        latitude: '',
        longitude: '',
        pageNum:1,
        totalNum:0,
        type:"2",
        key:"",
        popp:false,
        quyuId:"",
        museumId:"",
        size:4,
        list:[]
      }
    },
    methods:{
      we(){
        this.popp=!this.popp;
      },
      ss(){
       // this.key=this.$refs.lk.value;
        this.$http.get("/m/Guide/getGuideListByType.do",{size:this.size,area:this.quyuId,museumId:this.museumId,key:this.key,orderType:this.type,latitude:this.latitude,longitude:this.longitude}).then((res)=>{
          ;
          this.list=res.data.data;
          this.totalNum=res.data.page.totalPage;
        })
      },
      choose(num){
        if(num==0){
          this.tab=true;
          this.type="2";
          this.pageNum=1;
          this.museumId=""
        }else{
          this.tab=false;
          this.type="1";
          this.pageNum=1;
          this.museumId=""
        }
        this.$http.get("/m/Guide/getGuideListByType.do",{size:this.size,currentPage:this.pageNum,area:this.quyuId,museumId:this.museumId,key:this.key,orderType:this.type,latitude:this.latitude,longitude:this.longitude}).then((res)=>{
          this.list=res.data.data;
          this.totalNum=res.data.page.totalPage;
        })
      },
      gotoDetail(num){
        this.$router.push({path:'/GuideDeteil',query:{'id':num}});
      },
      getData(quyuId,museumId){
        this.popp=false;
        this.pageNum=1;
        this.quyuId=quyuId||"";
        this.museumId=museumId||"";
        this.$http.get("/m/Guide/getGuideListByType.do",{size:this.size,currentPage:this.pageNum,area:this.quyuId,museumId:this.museumId,key:this.key,orderType:this.type,latitude:this.latitude,longitude:this.longitude}).then((res)=>{
          this.list=res.data.data;
          this.totalNum=res.data.page.totalPage;
        })
      },
      getLatitude() {
        let vm = this;
        var geolocation = new BMap.Geolocation();
        Indicator.open({spinnerType: 'fading-circle'});
        geolocation.getCurrentPosition(function(r){
          Indicator.close();
          if(this.getStatus() == BMAP_STATUS_SUCCESS){
            vm.latitude = r.point.lat;
            vm.longitude = r.point.lng;
            vm.getData();
          } else {
            console.log('failed'+this.getStatus());
          }
        }, function() {
          Indicator.close();
        }, {enableHighAccuracy: true});
      },
      ch(){
        this.popp=false;
      },
      more(){
        if(this.pageNum==this.totalNum){
          Toast('已全部加载');
          return false;
        }else{
          this.pageNum++;
          this.$http.get("/m/Guide/getGuideListByType.do",{size:this.size,currentPage:this.pageNum,area:this.quyuId,museumId:this.museumId,key:this.key,orderType:this.type,latitude:this.latitude,longitude:this.longitude}).then((res)=>{
            ;
            this.list=this.list.concat(res.data.data);
            this.totalNum=res.data.page.totalPage;
          })
        }
      },
      shoucang(index){
        if(this.$refs.list[index].classList.contains("active")==false){
          this.$refs.list[index].classList.add("active");
        }else{
          this.$refs.list[index].classList.remove("active");
        }
      }
    },
    mounted(){
      this.getLatitude();
      /*this.$http.get("/m/Guide/getGuideListByType.do",{currentPage:this.pageNum,area:this.quyuId,museumId:this.museumId,key:this.key,type:this.type,latitude:this.latitude,longitude:this.longitude}).then((res)=>{
        ;
        this.data=res.data;
      })*/

    }
  }
</script>

<style lang="less" scoped>
  .main{
    width: 100%;
    .title{
      margin-top: 1px;
      height: 3.5rem;
      padding-top: 1rem;
      padding-left: 1.25rem;
      background: #fff;
      .list{
        height: 1.5rem;
      }
      input{
        width:9.9rem;
        height:1.5rem;
        border-radius:0.75rem;
        vertical-align: top;
        margin-left: 0.8rem;
        font-size:0.6rem;
        font-weight:400;
        color:rgba(185,185,185,1);
        line-height:1.5rem;
        padding-left: 1.8rem;
        background:rgb(241,241,241) url("../../assets/images/museum/sousuo.png") no-repeat 0.45rem 0.3rem;
      }
      span{
        display: inline-block;
        margin-left: 1rem;
        font-size:0.65rem;
        color:rgba(185,185,185,1);
        font-weight:400;
        vertical-align: top;
        height: 1.5rem;
        line-height: 1.5rem;
        i{
          vertical-align: top;
          &.active{
            color:rgba(72,165,255,1);
          }
        }
      }
      div{
        position: absolute;
        width: 1.3rem;
        height: 1.3rem;
        top: 1.25rem;
        left:4rem;
        z-index: 100;
      }
    }
    .line{
      background:rgba(241,241,241,1);
      height: 0.5rem;
    }
    .cont{
      width: 100%;
      background-color: #fff;
      position: absolute;
      top: 4rem;
      bottom: 0;
      overflow-y: scroll;
      .cont_wrap{
        width: 100%;
        padding: 1rem 1.25rem 2.45rem;
        .item{
          margin-top: 2rem;
          height:17.5rem;
          padding: .5rem 0 1rem;
          background:rgba(255,255,255,1);
          box-shadow: .5rem .5rem 1.25rem #EAEAEA;
          border-radius:0.75rem;
          &:nth-child(1){
            margin-top: 0;
          }
          .ti{
            font-size:0.65rem;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(102,102,102,1);
            line-height:0.9rem;
            .icon-info{
              width: .95rem;
              height: .95rem;
              background: url("../../assets/images/guide/useu.png") center no-repeat;
              background-size: 100% 100%;
              float: left;
              display: block;
              // margin-left: 1rem;
              margin-right: .25rem;
              border-radius: 100%;
            }
          }
          .info{
            margin-top: 0.3rem;
            span{
              font-size:0.65rem;
              font-weight:400;
              color:rgba(185,185,185,1);
              display: inline-block;
              height: 0.8rem;
              line-height: 0.8rem;
              i{
                vertical-align: top;
                margin-left: 0.5rem;
              }
              &:nth-child(2){
                display: inline-block;
                float: right;
                margin-right: 0.2rem;
              }
            }
          }
          .muimg{
            width:100%;
            height:9.15rem;
            display: block;
            margin-top: 0.45rem;
          }
          .display{
            margin-top: 1rem;
            span{
              &:nth-child(1){
                width:14.25rem;
                height:1.05rem;
                font-size:0.75rem;
                font-family:PingFang-SC-Bold;
                font-weight:bold;
                color:rgba(102,102,102,1);
                line-height:1.05rem;
                margin: 0 auto;
                display: block;
                overflow:hidden;
                text-overflow:ellipsis;
                white-space:nowrap
              }
              &:nth-child(2){
                font-size:0.75rem;
                font-weight:bold;
                color:rgba(102,102,102,1);
                line-height:1.05rem;
                margin-left: 0.5rem;
              }
            }
          }
          .disbottom{
            width:14.25rem;
            height: 2rem;
            margin: 0 auto;
            >p{
              height:1rem;
              font-size:1rem;
              font-family:PingFang-SC-Bold;
              font-weight:bold;
              color:rgba(78,77,77,1);
              line-height:1rem;
              float: right;
            }
            .flext{
              display: flex;
              justify-content: space-between;
              width:14.25rem;
              margin: 0 auto;
              >div{
                // width: 6rem;
                height:0.9rem;
                font-size:0.65rem;
                font-family:PingFang-SC-Regular;
                font-weight:400;
                color:rgba(185,185,185,1);
                display: flex;
                justify-content: space-around;
                line-height:0.9rem;
                i:nth-child(1){
                  background: url("../../assets/images/guide/Stroke1.png") center no-repeat;
                  background-size:100% 100%;
                  display: block;
                  width: .7rem;
                  height: .7rem;
                  margin-right: .3rem;
                }
                i:nth-child(3){
                  margin-left: .5rem;
                  background: url("../../assets/images/guide/group.png") center no-repeat;
                  background-size: 100% 100%;
                  display: block;
                  width: .7rem;
                  height: .7rem;
                  margin-right: .3rem;
                }
              }
              p{
                width:1.3rem;
                height:0.9rem;
                font-size:0.65rem;
                font-family:PingFang-SC-Regular;
                font-weight:400;
                color:rgba(102,102,102,1);
                line-height:0.9rem;
              }
            }
          }
          .distype{
            height:1rem;
            font-size:0.65rem;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            width:14.25rem;
            margin: .5rem auto 0rem;
            color:rgba(151,151,151,1);
            line-height:.9rem;
            text-align: center;
            span:first-child{
              height:1rem;
              border-radius:0.6rem;
              border:0.05rem solid #979797;
              float: left;
              padding: .05rem .63rem;
              margin-right: .5rem;
              display: block;
            }
            span:last-child{
              height:1rem;
              border-radius:0.6rem;
              display: block;
              float: left;
              padding: .05rem .63rem;
              border:0.05rem solid #979797;
            }
          }
        }
        .more{
          width:16.25rem;
          height:1.5rem;
          background:rgba(243,245,250,1);
          border-radius:0.25rem;
          font-size:0.65rem;
          text-align: center;
          font-weight:bold;
          color:rgba(91,102,160,1);
          line-height:1.5rem;
          margin:1.3rem 0 1.3rem;
          touch-action: none;
        }
      }
    }
  }

</style>
<style>
  html,body,.main{
    width:100%;
    height: 100%;
  }
</style>



