<template>
  <div class="page-my">
    <div class="m-my">
      <div class="wrap-top">
        <img class="top-bg" :src="userData.avatarurl" alt="" :onerror="defaultHeadImg">

        <div class="top-info-wrap" flex="dir:left main:center cross:center">
          <div flex="dir:top">
            <div class="top-info" flex="dir:left main:justify cross:center">
              <router-link tag="a" to="/my/info"><i class="g-icon icon-my-person"></i></router-link>
              <div class="pic-box">
                <img :src="userData.avatarurl" alt="" :onerror="defaultHeadImg">
              </div>
              <router-link tag="a" to="/my/passModify"><i class="g-icon icon-my-lock"></i></router-link>
            </div>

            <div class="top-des" flex="dir:top main:center cross:center">
              <h1 class="ell">{{userData.nickName}}</h1>
              <p class="two-ell">{{userData.description || '这个人很懒，什么都没有留下'}}</p>
            </div>
          </div>
        </div>
      </div>

      <!-- 内容 start -->
      <keep-alive>
        <router-view ref="myList" v-on:emitGetData="getData" v-on:emitMore="more" v-on:emitDeleCollect="deleSure" v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
      <router-view ref="myList" v-on:emitGetData="getData" v-on:emitMore="more" v-on:emitDeleCollect="deleSure" v-if="!$route.meta.keepAlive"></router-view>
      <!-- 内容 end -->
    </div>
  </div>
</template>

<script>
import { MessageBox, Toast } from 'mint-ui'
import { utilSlide } from '@/utils/util'
export default {
  data() {
    return {
      userData: '',
      defaultHeadImg: this.$store.getters.defaultHeadImg
    }
  },
  methods: {
    getUserInfoData() {
      this.$http.get('/m/myInfo/userInfo.do', {
        hideLoadig: true,
        userID: this.$store.getters.userid,
        token: this.$store.getters.token
      }).then(response => {
        if(response.data.success == 1) {
          let data = response.data.data.user;
          this.userData = data;
        } else {
          this.$router.push({ path: '/my/login'});
        }
      })
    },
    more(params) {
      let vmChild = this.$refs.myList; 
      if(vmChild.isTotal) {
        Toast('已全部加载');
        return false;
      } else {
        this.$http.get('/m/myInfo/myCollects.do', params)
        .then((res)=>{
          if(res.data.success == 1) { 
            let data = res.data.data;
            if(vmChild.currentPage === res.data.page.totalPage) {
              vmChild.isTotal = true;
            } else {
              vmChild.isTotal = false;
            }
            vmChild.listData = vmChild.listData.concat(data);

          } else if(res.data.success == 999) {
            this.$common.confirmLogin();
          } else {
            Toast({
              message: res.data.data || '接口异常',
              position: 'bottom'
            });
          }
          
        })
      }
    },
    // 我的列表
    getData(params) {
      let vmChild = this.$refs.myList; 
      this.$http.get('/m/myInfo/myCollects.do', params)
      .then((res)=>{
        if(res.data.success == 1) {
          let data = res.data.data;
          if(vmChild.currentPage === res.data.page.totalPage) {
            vmChild.isTotal = true;
          } else {
            vmChild.isTotal = false;
          }
          vmChild.listData = data;
        } else if(res.data.success == 999) {
          this.$common.confirmLogin();
        } else {
          Toast({
            message: res.data.data || '接口异常',
            position: 'bottom'
          });
        }
      })
    },
    deleSure(params) {
      MessageBox({
        title: '',
        message: '是否确认删除该收藏?',
        showCancelButton: true
      }).then(action => {
        utilSlide.restSlide();
        if(action === 'confirm') {
          this.deleCollect(params);
        }
      });
    },
    deleCollect(params) {
      let vmChild = this.$refs.myList; 

      this.$http.get('/m/myInfo/delCollect.do', params)
      .then((res)=>{
        if(res.data.success == 1) { 
          utilSlide.restSlide();
          Toast({
            message: '删除成功！',
            position: 'center',
            duration: 2000,
            iconClass: 'icon icon-success'
          });
          vmChild.currentPage = 1;
          vmChild.getData();
        } else {
          Toast({
            message: '删除失败！',
            position: 'center',
            duration: 2000,
            iconClass: 'icon icon-fail'
          });
        }
        
      })
    }
    
  },
  mounted() {
    this.getUserInfoData();
  }
}
</script>

<style lang="less" scoped>
  @import '../../assets/css/mixin.less';
  .page-my {
    .m-my {
      width: 100%;
      height: 100%;
      .wrap-top {
        position: fixed;
        width: 100%;
        height: 12.9rem;
        overflow: hidden;
        z-index: 2;
        // background:rgba(0,0,0,0.3);
        .top-bg {
          width: 100%;
          height: 100%;
          position: absolute;
          left: 0;
          top:0;
          background:rgba(0,0,0,0.7);
          filter: blur(6px);
          transform: scale(1.2);
        }
        .top-info-wrap {
          position: relative;
          width: 100%;
          height: 100%;
          background:rgba(0,0,0,0.3);
          .top-info {
            z-index: 2;
            width: 10.4rem;
            .pic-box {
              width: 5rem;
              height: 5rem;
              img {
                border-radius: 100%;
                width: 100%;
                height: 100%;
              }
            }
            .icon-my-person {
              #f-bg(1.2rem, 1.2rem, "../images/my/icon-my-person.png");
            }
            .icon-my-lock {
              #f-bg(1.2rem, 1.2rem, "../images/my/icon-my-lock.png");
            }
          }
        }
  
        .top-des {
          margin-top: 1rem;
          color: #fff;
          font-weight: 400;
          word-break: break-all;
          h1 {
            width: 10rem;
            margin-bottom: .25rem;
            font-size: .85rem;
            line-height: 1.2rem;
            text-align: center;
          }
          p{
            width: 10rem;
            font-size:0.65rem;
            line-height: .9rem;
            text-align: center;
          }
        }
      }
    }
  }
</style>





