<template>
  <div class="page-register">
    <div class="register" flex="dir:top">
      <div class="item-wrap item-phone" flex="dir:left box:mean">
        <input type="text" placeholder="请输入手机号码" v-model="phoneNo">
      </div>
      <div class="item-wrap item-vertify" flex="dir:left main:justify cross:center box:last">
        <input type="text" placeholder="验证码" v-model="code">
        <span :class="{enable: codeEnable}" @click="sendMsg" v-if="!resetMsg">获取验证码</span>
        <span v-if="resetMsg">重新发送({{resetMsg}}s)</span>
      </div>
      <div class="item-wrap item-password" flex="dir:left main:justify cross:center box:last">
        <input placeholder="设置6至10位登录密码" :type="passType" v-model="pass" @keyup.enter="goRegister">
        <i :class="['eye-icon', passType=='password'?'eye-icon-close':'eye-icon-open']" @click="passType=passType=='password'?'text':'password'"></i>
      </div>
    </div>
    <div flex="dir:left main:center">
      <a class="submit" href="javascript:;" 
        :class="{enable: submitEnable}" 
        @click="goRegister">{{pageType === 'add' ? '注册' : '确认'}}</a>
      </div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
import { validate } from '@/utils/util';
export default {
  data() {
    return {
      pageType: '',
      phoneNo: '',
      code: '',
      codeVetify: '', // 短信验证码
      pass: '',
      resetMsg: '',
      passType: 'password',
      codeEnable: false,  // 验证码按钮是否可用
      passEnable: false  // 密码是否通过验证
    }
  },
  watch: {
    phoneNo(newVal, oldVal) {
      if(validate(newVal, 'phone')) {
        this.codeEnable = true;
      } else {
        this.codeEnable = false;
      }
    }
  },
  computed: {
    // 注册按钮是否可用
    submitEnable() {
      if(validate(this.phoneNo, 'phone') && this.code && this.pass) {
        return true;
      }
      return false;
    }
  },
  methods: {
    // 获取验证码
    sendMsg() {
      if(this.codeEnable) {
        this.timeDown();
        // 发送验证码
        this.$http.get('/m/sendSecretCode.do', {
          phone: this.phoneNo,
          type: this.pageType == 'add' ? 1 : 2
        }).then(response => {
          if(response.data.success == 1) {
            this.codeVetify = response.data.data;
          } else {
            Toast({
              message: response.data.data,
              position: 'center'
            });
          }
        }, error => {
          console.log('异常');
        })
      }
    },
    // 倒计时
    timeDown() {
      this.resetMsg = 60;
      let times = setInterval(() => {
        if (this.resetMsg <= 0) {
          this.resetMsg = '';
          clearInterval(times);
        } else {
          this.resetMsg--;
        }
      }, 1000)
    },
    goRegister() {
      if(!this.submitEnable) { return; }
      if(this.code !== this.codeVetify) {
        Toast({
          message: '验证码错误',
          position: 'bottom'
        });
        return;
      }
      if(!validate(this.pass, 'password')) {
        Toast({
          message: '请设置6至10位登录密码，至少包含一位数字和字母',
          position: 'bottom'
        });
        return;
      }
      this.register();
    },
    // 注册、修改密码
    register() {
      let api = this.pageType == 'add' ? '/m/register.do' : '/m/resetPassword.do';
      this.$http.get(api, {
        phone: this.phoneNo,
        secretCode: this.code,
        password: this.pass
      }).then(response => {
        if(response.data.success == 1) {
          this.$router.push({ path: '/my/login'});
        } else {
          Toast({
            message: response.data.data,
            position: 'center'
          });
        }
      }, error => {
        console.log('异常');
      })
    }
  },
  mounted() {
    this.pageType = this.$route.query.type;  // add 注册   forget 忘记密码
  }
}
</script>

<style lang="less" scoped>
  @import '../../assets/css/mixin.less';
  .page-register {
    padding: 5rem 2.25rem;
    font-size: 16px;
    .register {
      .item-wrap {
        position: relative;
        padding: 0 1rem;
        height:2.2rem;
        background:rgba(241,241,241,1);
        border-radius: 1.1rem;
        margin-bottom: 1rem;
        input {
          display: inline-block;
          font-size: 16px;
          // font-size: .65rem;
          line-height: 1.2rem;
          background: transparent;
          color: #666;
          &::-webkit-input-placeholder {
            color: #B9B9B9;
          }
          &:-moz-placeholder {
            color: #B9B9B9;
          }
          &:-ms-input-placeholder {
            color: #B9B9B9;
          }
        }
      }
      .item-vertify {
        span {
          padding: 0 0.2rem;
          display: inline-block;
          color: #999;
          font-size: 14px;
          // font-size: .65rem;
          text-align: center;
          &.enable {
            color: #48A5FF;
          }
        }
      }
    }
    .submit {
      width: 100%;
      text-align: center;
      margin-top: .8rem;
      height:2.2rem;
      line-height: 2.2rem;
      background: #999;
      border-radius:1.1rem;
      color: #fff;
      font-size: 18px;
      // font-size: .8rem;
      &.enable {
        background:linear-gradient(126deg,rgba(72,165,255,1) 0%,rgba(129,226,255,1) 100%);
      }
    }
  }
</style>





