import Vue from 'vue'
import Router from 'vue-router'
import Demo from '@views/demo/index'

import museumList from '@views/museum/museumList'    //博物馆列表
import museumDetail from '@views/museum/museumDetail'    //博物馆详情
import display from '@views/display/index'    //展览列表
import displayDetail from '@views/display/displayDetail'    //展览详情
import activity from '@views/activity/index'    //活动列表
import activityDetail from '@views/activity/activityDetail'    //活动详情
import information from '@views/information/index'    //资讯列表
import informationDetail from '@views/information/informationDetail'    //资讯详情
import wenchuang from '@views/wenchuang/index'    //文创列表
import wenchuangDetail from '@views/wenchuang/wenchuangDetail'    //文创详情
import collect from '@views/collect/index'    //藏品列表
import collectDetail from '@views/collect/collectDetail'    //藏品详情

import Home from '@views/home'                   // 首页
import Login from '@views/my/login'              // 登录
import Register from '@views/my/register'        // 注册、忘记密码
// import PassForget from '@views/my/passForget'    // 忘记密码
import PassModify from '@views/my/passModify'    // 修改密码
import My from '@views/my'                       // 我的博物馆
import MyMuseum from '@views/my/museum'          // 我的博物馆
import MyCollect from '@views/my/collect'        // 我的收藏
import MyExhibition from '@views/my/exhibition'  // 我的展览
import MyActivity from '@views/my/activity'      // 我的活动
import MyInfo from '@views/my/info'          // 个人信息


import Guide from '@views/guide/index' //导览
import Digital from '@views/digital/index' //数字展厅
import GuideDeteil from '@views/guide/detail' //导览详情
import Tabdetail from '@views/guide/tabdetail' //导览详情
import Tabspeaker from '@views/guide/tabspeaker' //导览详情
import ThemeList from '@views/guide/themeList' //导览详情
import ThemeDetail from '@views/guide/themeDetail' //导览详情


Vue.use(Router)

export default new Router({
  routes: [
    {
      title: 'sss',
      path: '/demo',
      name: 'Demo',
      component: Demo,
      meta: { keepAlive: false }
    },
    {
      title: '导览',
      path: '/Guide',
      name: 'Guide',
      component: Guide,
      meta: { keepAlive: false }
    },
    {
      title: '数字展厅',
      path: '/Digital',
      name: 'Digital',
      component: Digital,
      meta: { keepAlive: false }
    },
    {
      title: '语音导览',
      path: '/GuideDeteil',
      name: 'GuideDeteil',
      component: GuideDeteil,
      meta: { keepAlive: false }
    },
    {
      title: '语音导览',
      path: '/ThemeList',
      name: 'ThemeList',
      component: ThemeList,
      meta: { keepAlive: false }
    },
    {
      title: '语音导览',
      path: '/ThemeDetail',
      name: 'ThemeDetail',
      component: ThemeDetail,
      meta: { keepAlive: false }
    },
    {
      title: '藏品专题',
      path: '/GuideDeteil',
      name: 'GuideDeteil',
      component: GuideDeteil,
      meta: { keepAlive: false }
    },
    {
      title: '博物馆列表',
      path: '/museumList',
      name: 'museumList',
      component: museumList,
      meta: { keepAlive: false }
    },
    {
      title: '博物馆详情',
      path: '/museumDetail',
      name: 'museumDetail',
      component: museumDetail,
      meta: { keepAlive: false }
    },
    {
      title: '展览列表',
      path: '/display',
      name: 'display',
      component: display,
      meta: { keepAlive: false }
    },
    {
      title: '展览详情',
      path: '/displayDetail',
      name: 'displayDetail',
      component: displayDetail,
      meta: { keepAlive: false }
    },
    {
      title: '活动列表',
      path: '/activity',
      name: 'activity',
      component: activity,
      meta: { keepAlive: false }
    },
    {
      title: '活动详情',
      path: '/activityDetail',
      name: 'activityDetail',
      component: activityDetail,
      meta: { keepAlive: false }
    },
    {
      title: '资讯列表',
      path: '/information',
      name: 'information',
      component: information,
      meta: { keepAlive: false }
    },
    {
      title: '资讯详情',
      path: '/informationDetail',
      name: 'informationDetail',
      component: informationDetail,
      meta: { keepAlive: false }
    },
    {
      title: '文创列表',
      path: '/wenchuang',
      name: 'wenchuang',
      component: wenchuang,
      meta: { keepAlive: false }
    },
    {
      title: '文创详情',
      path: '/wenchuangDetail',
      name: 'wenchuangDetail',
      component: wenchuangDetail,
      meta: { keepAlive: false }
    },
    {
      title: '藏品列表',
      path: '/collect',
      name: 'collect',
      component: collect,
      meta: { keepAlive: false }
    },
    {
      title: '藏品详情',
      path: '/collectDetail',
      name: 'collectDetail',
      component: collectDetail,
      meta: { keepAlive: false }
    },
    {
      title: '动态',
      path: '/',
      name: 'Home',
      component: Home,
      meta: { keepAlive: false }
    },
    {
      title: '登录',
      path: '/my/login',
      name: 'Login',
      component: Login,
      meta: { keepAlive: false }
    },
    {
      title: '注册',
      path: '/my/register',
      name: 'Register',
      component: Register,
      meta: { keepAlive: false }
    },
    // {
    //   title: '忘记密码',
    //   path: '/my/passForget',
    //   name: 'PassForget',
    //   component: PassForget,
    //   meta: { keepAlive: false }
    // },
    {
      title: '修改密码',
      path: '/my/passModify',
      name: 'PassModify',
      component: PassModify,
      meta: { keepAlive: false }
    },
    {
      title: '我的',
      path: '/my',
      name: 'My',
      component: My,
      meta: { keepAlive: false },
      redirect:'/my/museum',
      children: [
        {
          title: '我的博物馆',
          path: '/my/museum',
          name: 'MyMuseum',
          component: MyMuseum,
          meta: { keepAlive: false }
        },
        {
          title: '我的藏品',
          path: '/my/collect',
          name: 'MyCollect',
          component: MyCollect,
          meta: { keepAlive: false }
        },
        {
          title: '我的展览',
          path: '/my/exhibition',
          name: 'MyExhibition',
          component: MyExhibition,
          meta: { keepAlive: false }
        },
        {
          title: '我的活动',
          path: '/my/activity',
          name: 'MyActivity',
          component: MyActivity,
          meta: { keepAlive: false }
        }
      ]
    },
    {
      title: '个人信息',
      path: '/my/info',
      name: 'MyInfo',
      component: MyInfo,
      meta: { keepAlive: false }
    }
  ]
})



