<template>
  <div class="cmp-footer" flex="dir:bottom">
    <div class="footer-bg"></div>
    <div class="icon-wrap" flex="dir:left box:mean">
      <router-link tag="div" to="/" flex="dir:top main:center cross:center">
        <div :class="['icon-item', navType==1?'active':'']" flex="dir:top main:right cross:center">
          <i class="g-icon icon-home"></i>
          <span class="name">动态</span>
        </div>
      </router-link>
      <router-link tag="div" to="/Guide" flex="dir:top main:center cross:center">
        <div :class="['icon-item', navType==2?'active':'']" flex="dir:top main:right cross:center">
          <i class="g-icon icon-loc"></i>
          <span class="name">导览</span>
        </div>
      </router-link>
      <router-link tag="div" to="/Digital" flex="dir:top main:center cross:center">
        <div :class="['icon-item', navType==3?'active':'']" flex="dir:top main:right cross:center">
          <i class="g-icon icon-num"></i>
          <span class="name">数字展厅</span>
        </div>
      </router-link>
      <div flex="dir:top main:center cross:center" @click="goPageMy">
        <div :class="['icon-item', navType==4?'active':'']" flex="dir:top main:right cross:center">
          <i class="g-icon icon-my"></i>
          <span class="name">我的</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'cmpFooter',
  props: {
    navType: {
      type: Number,
      default: '1'
    }
  },
  data() {
    return {

    }
  },
  methods: {
    goPageMy() {
      if(!this.$store.getters.isLogin) {
        this.$router.push('/my/login');
      } else {
        this.$router.push('/my');
      }
    }
  },
  mounted() {
    
  }
}
</script>

<style lang="less" scoped>
  @import '../assets/css/mixin.less';
  .cmp-footer {
    width: 100%;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 9;
    .footer-bg {
      width: 100%;
      height: 2.45rem;
      background: #fff;
      position: absolute;
      bottom: 0;
      left: 0;
      border-top: solid 1px #ececec;
    }
    .icon-wrap {
      position: relative;
      z-index: 2;
      .icon-item {
        width: 2.5rem;
        height: 2.75rem;
        padding-top: .2rem;
        color: #666;
        font-size: .5rem;
        font-weight: 400;
        line-height: 1rem;
        .icon-home {
          width: 1rem;
          height: 1rem;
          #f-bg-url("../images/icon-home.png")
        }
        .icon-loc {
          width: 1rem;
          height: 1rem;
          #f-bg-url("../images/icon-loc.png")
        }
        .icon-num {
          width: 1rem;
          height: 1rem;
          #f-bg-url("../images/icon-num.png")
        }
        .icon-my {
          width: .8rem;
          height: 1rem;
          #f-bg-url("../images/icon-my.png")
        }
        &.active {
          background: #fff;
          color: #48A5FF;
          box-shadow: 0 0 0.4rem 0 #888;
          // box-shadow: 0rem 0rem 0.4rem 0rem rgba(0,0,0,0.08);
          border-radius: 5rem 5rem 0rem 0rem;
          // border: solid 1px rgba(0,0,0,0.08);
          // border-bottom: none;
          .icon-home {
            #f-bg-url("../images/icon-home.gif")
          }
          .icon-loc {
            #f-bg-url("../images/icon-loc.gif")
          }
          .icon-num {
            #f-bg-url("../images/icon-num.gif")
          }
          .icon-my {
            #f-bg-url("../images/icon-my.gif")
          }
        }
      }
    }
  }
</style>





